<template>
  <b-container class="nuevoCobro">
    <div v-if="loading" class="py-5 text-center box-shadow">
      <span class="text-danger">
        <b>Cargando</b>
      </span>
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
    </div>
    <div v-else class="box-shadow p-2 mx-1">
      <b-row class="pb-3">
        <b-col lg="2" sm="4" class="text-center"></b-col>
        <b-col md="8" sm="6" class="text-left pt-3">
          <div>
            <p class="h4 text-center">Cobranzas</p>
          </div>
        </b-col>
      </b-row>
      <ValidationObserver ref="nuevoCobro">
        <b-container class="pb-3">
          <b-form-group>
            <b-alert
              v-for="alert in alerts"
              dismissible
              v-bind:key="alert.key"
              show
              :variant="alert.variant"
              >{{ alert.message }}</b-alert
            >
          </b-form-group>
          <b-card class="mb-3" header="Persona a buscar" no-body>
            <b-tabs pills card>
              <b-tab title="DNI o Socio" active>
                <b-row>
                  <b-col sm="10" lg="10">
                    <b-form-group
                      v-if="!iscobradoEmpresa"
                      id="DNI-label"
                      label="DNI:"
                      label-for="DNI"
                    ></b-form-group>
                    <b-form-group
                      v-if="iscobradoEmpresa"
                      id="DNI-label"
                      label="DNI-Socio:"
                      label-for="DNI"
                    ></b-form-group>
                    <ValidationProvider
                      :name="'Persona '"
                      :rules="'required|max:10'"
                      v-slot="{ errors, valid }"
                    >
                      <!-- v-hotkey="{ enter: checkPersona }" -->
                      <b-form-input
                        v-focus
                        id="DNI-nroSocio"
                        type="number"
                        v-model="persona.dniOsocio"
                        v-on:keyup.enter="checkPersona"
                        @click="vaciarCampos()"
                        size="sm"
                        required
                        :state="
                          errors[0] || persona.error
                            ? false
                            : valid
                            ? true
                            : null
                        "
                        placeholder="persona a buscar"
                      ></b-form-input>
                      <b-form-invalid-feedback
                        v-for="error in errors"
                        :key="error.key"
                        >{{ error }}</b-form-invalid-feedback
                      >
                      <b-form-invalid-feedback v-if="persona.error">{{
                        persona.error
                      }}</b-form-invalid-feedback>
                    </ValidationProvider>
                  </b-col>
                  <b-col sm="2" lg="2" class="mt-sm-2 pt-sm-4">
                    <div v-if="!buscandoPersona">
                      <b-button
                        class="mr-2"
                        variant="primary"
                        size="sm"
                        @click="checkPersona()"
                        >Buscar</b-button
                      >
                    </div>
                    <div v-else>
                      <b-spinner
                        variant="primary"
                        label="Text Centered"
                      ></b-spinner>
                    </div>
                  </b-col>
                </b-row>
              </b-tab>
              <b-tab title="Nombre y Apellido">
                <b-row>
                  <b-col sm="10">
                    <b-form-group
                      v-if="iscobradoEmpresa"
                      id="nombreApellido"
                      label="Busqueda por nombre apellido:"
                      label-for="nombreApellido"
                    >
                      <b-form-input
                        v-focus
                        id="nombre-apellido"
                        type="text"
                        v-model="searchPerson.nyap"
                        size="sm"
                        :state="searchPerson.error ? false : null"
                        required
                        @keyup.enter="buscarPersonas()"
                        placeholder="Busqueda por apellido , nombre"
                      ></b-form-input>
                      <b-form-invalid-feedback v-if="searchPerson.error">
                        {{ searchPerson.errorDesc }}
                      </b-form-invalid-feedback>
                    </b-form-group>
                  </b-col>
                  <b-col sm="2" lg="2" class="mt-sm-2 pt-sm-4">
                    <div v-if="!findPerson">
                      <b-button
                        class="mr-2"
                        variant="primary"
                        size="sm"
                        @click="buscarPersonas()"
                        >Buscar</b-button
                      >
                    </div>
                    <div v-else>
                      <b-spinner
                        variant="primary"
                        label="Text Centered"
                      ></b-spinner>
                    </div>
                  </b-col>
                </b-row>

                <b-row v-if="personas">
                  <b-col>
                    <b-table
                      class="flip-list-move"
                      ref="selectableTable"
                      small
                      v-show="personas.length > 0"
                      responsive
                      :items="personas"
                      :fields="['documento', 'nombre', 'selected']"
                      :current-page="currentPage"
                      :per-page="perPage"
                      selectable
                      :select-mode="selectMode"
                      @row-selected="onRowSelected"
                    >
                      <!-- @row-selected="onRowSelected" -->
                      <div
                        slot="table-busy"
                        class="text-center text-danger my-2"
                      >
                        <b-spinner class="align-middle"></b-spinner>
                        <strong>Cargando...</strong>
                      </div>

                      <template v-slot:cell(documento)="row">
                        {{ row.item.nroDoc }}
                      </template>
                      <template v-slot:cell(nombre)="row">
                        {{ row.item.nombre }}
                      </template>
                      <template v-slot:cell(selected)="{ rowSelected }">
                        <template v-if="rowSelected">
                          <span aria-hidden="true">&check;</span>
                        </template>
                        <template v-else>
                          <span aria-hidden="true">&nbsp;</span>
                          <span class="sr-only">Not selected</span>
                        </template>
                      </template>
                    </b-table>
                  </b-col>
                </b-row>
                <b-row v-show="personas.length > 0">
                  <b-col cols="12" justify-content-center>
                    <b-pagination
                      v-model="currentPage"
                      :total-rows="totalRows"
                      :per-page="perPage"
                      align="center"
                      size="sm"
                      class="my-0"
                    ></b-pagination>
                  </b-col>
                </b-row>
                <b-row>
                  <b-col cols="12" justify-content-center>
                    <b-btn
                      :disabled="!!!personaSelected"
                      variant="primary"
                      size="sm"
                      @click="setPersona()"
                    >
                      Seleccionar persona
                    </b-btn>
                  </b-col>
                </b-row>
              </b-tab>
            </b-tabs>
          </b-card>

          <b-col v-if="this.message">
            <b-alert show variant="primary">{{ message }}</b-alert>
          </b-col>

          <b-card
            class="mb-3"
            header="Persona Seleccionada"
            v-if="persona.nombreYapellido"
          >
            <b-card-text>
              <b-row>
                <b-col v-if="persona.nombreYapellido">
                  <span class="font-weight-bold">Nombre y Apellido:</span>
                  {{ persona.nombreYapellido }}
                </b-col>
                <b-col v-if="persona.dniOsocio">
                  <span class="font-weight-bold">DNI:</span>
                  {{ persona.dniOsocio }}
                </b-col>
              </b-row>
          
            </b-card-text>
          </b-card>
          <b-card
            class="mb-3"
            header="Pagos Pendientes"
            v-if="this.facturas.length > 0"
          >
            <b-row>
              <ListadoFacturasCobrar :items="facturas"></ListadoFacturasCobrar>
            </b-row>
          </b-card>
        </b-container>
      </ValidationObserver>
    </div>
  </b-container>
</template>


<script>
import { mapGetters } from "vuex";
import ListadoFacturasCobrar from "@/components/facturas/ListadoFacturasCobrar.vue";
import FacturaService from "@/services/FacturaService";
import CobradorService from "@/services/CobradorService";
export default {
  name: "nuevaFactura",
  components: {
    ListadoFacturasCobrar,
  },
  data() {
    return {
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, 20],
      transProps: {
        name: "flip-list",
      },
      selectMode: "single",

      facturas: [],
      facturasPersona: 0,
      message: "",
      alerts: [],
      buscandoPersona: false,
      findPerson: false,
      loading: true,
      iscobradoEmpresa: false,
      persona: {
        dni: "",
        nroSocio: "",
        nombreYapellido: "",
        dniOsocio: "",
        error: "",
      },
      searchPerson: {
        nyap: "",
        error: false,
        errorDesc: "",
      },
      personas: [],
      personaSelected: null,
    };
  },
  directives: {
    focus: {
      // Definición de directiva
      inserted: function (el) {
        el.focus();
      },
    },
  },
  computed: {
    ...mapGetters("storeUser", ["getUserId"]),

    totalRows() {
      return this.personas.length;
    },
  },
  methods: {
    onRowSelected(items) {
      this.vaciarCampos();
      if (items.length > 0) {
        this.personaSelected = items[0]["nroDoc"];
      } else {
        this.personaSelected = null;
      }

      //console.log (this.personaSelected);
    },

    setPersona() {
      // console.log (this.personaSelected);
      this.persona.dniOsocio = this.personaSelected;
      this.checkPersona();
    },

    vaciarCampos() {
      this.persona.nombreYapellido = "";
      this.persona.dni = "";
      this.persona.nroSocio = "";
      this.facturas = [];
      this.message = "";
      this.persona.dniOsocio = "";
    },

    async buscarPersonas() {
      if (!this.searchPerson.nyap) {
        this.searchPerson.error = true;
        this.searchPerson.errorDesc = "ingrese nombre o apellido a buscar";
        this.personas = [];
      } else if (this.searchPerson.nyap.length < 3) {
        this.searchPerson.error = true;
        this.searchPerson.errorDesc =
          "debe ingresar al menos 3 caracteres para buscar por nombre o apellido";
        this.personas = [];
      } else {
        this.searchPerson.error = false;
        this.personas = [];

        try {
          this.findPerson = true;
          const response = await FacturaService.getPersonas({
            nyap: this.searchPerson.nyap,
          });

          this.personas = response.data.data;
        } catch (error) {
          this.$bvToast.toast(error.response.data.message, {
            title: "Atencion!",
            toaster: "b-toaster-top-center",
            solid: true,
            variant: "danger",
          });
        }
        this.findPerson = false;
      }
    },

    async checkPersona() {
      try {
        this.buscandoPersona = true;
        const response = await FacturaService.getFacturasdeCliente({
          cobrador: this.getUserId,
          dniOsocio: this.persona.dniOsocio,
        });
        this.facturas = response.data.data.facturas;
        this.persona.nombreYapellido = response.data.data.usuario;
      } catch (err) {
        this.$bvToast.toast(err.response.data.message, {
          title: "Atencion!",
          toaster: "b-toaster-top-center",
          solid: true,
          variant: "danger",
        });
      }
      this.buscandoPersona = false;
      if (this.facturas.length > 0) {
        this.message = "";
        this.calcularDebeFacturas(this.facturas);
      } else {
        this.message = this.persona.nombreYapellido
          ? "La persona no posee facturas pendientes"
          : "La persona no se encuentra en el sistema";
      }
    },
    calcularDebeFacturas(facturas) {
      var fechaHoy = new Date();
      fechaHoy.setHours(0, 0, 0, 0);
      this.persona.nombreYapellido = facturas[0].nombre;
      var importeFactura = 0;
      facturas.forEach(function (factura) {
        var fechaFactura = new Date(factura.fechavencimiento1);
        fechaFactura.setHours(0, 0, 0, 0);
        importeFactura =
          fechaHoy.getTime() <= fechaFactura.getTime()
            ? factura.importe
            : factura.importe2;
        importeFactura = Number(importeFactura) - Number(factura.saldo);
        factura.debe = importeFactura;
      });
    },
    async esCobradorEmpresa() {
      try {
        const response = await CobradorService.esCobradorEmpresa({
          cobrador: this.getUserId,
        });
        this.iscobradoEmpresa = response.data.data;
      } catch (err) {
        this.$bvToast.toast(err.response.data.message, {
          title: "Atencion!",
          toaster: "b-toaster-top-center",
          solid: true,
          variant: "danger",
        });
      }
    },
  },
  mounted() {
    this.esCobradorEmpresa().then(() => {
      this.loading = false;
    });
  },
};
</script>

