<template>
  <b-container class="listadoRendiciones">
    <br />
    <div v-if="loading" class="py-5 text-center box-shadow">
      <span class="text-danger">
        <b>Cargando</b>
      </span>
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
    </div>
    <div v-else class="box-shadow p-2 mx-1">
      <b-row class="pb-3">
        <b-col lg="2" sm="4" class="text-center"></b-col>
        <b-col md="8" sm="6" class="text-left pt-3">
          <div>
            <p class="h4 text-center">Mis Rendiciones</p>
          </div>
        </b-col>
        <b-col md="4" class="my-1">
          <b-form-group class="mb-0">
            <b-input-group>
              <b-form-input
                v-model="filterPrev"
                @keyup.enter="filter = filterPrev"
                size="sm"
                placeholder="Buscar por cobrador o empresa"
              ></b-form-input>
              <b-input-group-append>
                <b-button size="sm" @click="filter = filterPrev"
                  >Buscar</b-button
                >
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>
      </b-row>
      <b-table
        show-empty
        small
        responsive
        :busy.sync="isBusy"
        :items="items"
        :fields="fields"
        :filter="filter"
        :current-page="currentPage"
        :per-page="perPage"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
      >
        <template v-slot:cell(estado)="row">{{
          getEstado(row.item.estado)
        }}</template>
        <template v-slot:cell(acciones)="row">
          <b-button
            v-if="(row.item.estado == '03') & (getGrupos[0] == ['EMPRESA'])"
            type="button"
            @click="rendirRendicion(row.item)"
            variant="primary"
            >Rendir</b-button
          >
          <b-button
            type="button"
            @click="verRecibos(row.item)"
            variant="secondary"
          >
            <b-icon icon="eye"></b-icon>
          </b-button>
          <b-button
            v-if="row.item.estado == '03'"
            type="button"
            @click="descargarRendicionFormatPDF(row.item)"
            variant="danger"
          >
            <b-icon icon="download" font-scale="1"></b-icon>
          </b-button>
        </template>
        <template v-slot:cell(fecha)="row">{{
          row.item.fecha | formatDate
        }}</template>
        <template v-slot:cell(importe)="row">{{
          getImporte(row.item.importe)
        }}</template>
        <template slot="table-caption" v-if="this.items.length"
          >Mostrando {{ this.rows }} de
          {{ this.items.length }} entradas</template
        >
      </b-table>
      <b-row class="mt-auto">
        <b-col md="6" class="my-1">
          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            class="my-0"
          ></b-pagination>
        </b-col>
        <b-col md="6" class="my-1">
          <b-row align-h="end">
            <b-col cols="5">
              <b-form-group label-cols="2" label="Ver" class="mb-0">
                <b-form-select
                  v-model="perPage"
                  :options="pageOptions"
                ></b-form-select>
              </b-form-group>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </div>

    <div id="ModalRecibos">
      <b-modal size="lg" ref="modalRecibos" ok-only>
        <ListadoRecibos :items="recibos"></ListadoRecibos>
      </b-modal>
    </div>
  </b-container>
</template>   

<script>
import ReciboService from "@/services/ReciboService";
import RendicionService from "@/services/RendicionService";
import ListadoRecibos from "@/components/recibo/ListadoRecibos.vue";
import { mapGetters } from "vuex";
import jsPDF from "jspdf";
import moment from "moment";
export default {
  components: {
    ListadoRecibos,
  },
  props: {
    tipo: {
      type: Array,
    },
  },
  name: "ListadoRendiciones",
  data() {
    return {
      loading: true,
      isBusy: false,
      recibos: [],
      rendiciones: [],
      estados: [],
      fields: [
        {
          key: "empresa.nombre",
          label: "Empresa",
          class: "text-left",
          sortable: true,
        },
        {
          key: "cobrador.nombre",
          label: "Cobrador",
          class: "text-left",
          sortable: true,
        },
        { key: "fecha", label: "Fecha", class: "text-left", sortable: true },
        {
          key: "importe",
          label: "importe",
          class: "text-right",
          sortable: true,
        },
        { key: "estado", label: "Estado", class: "text-right", sortable: true },
        {
          key: "acciones",
          label: "Acciones",
          class: "text-right",
          sortable: true,
        },
      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      sortBy: "",
      sortDesc: false,
      filter: null,
      filterPrev: null,
      sortDirection: "asc",
      pageOptions: [10, 15, 20],
      transProps: {
        name: "flip-list",
      },
    };
  },
  computed: {
    ...mapGetters("storeUser", ["getUserId", "getGrupos"]),
    items() {
      return this.filter
        ? this.rendiciones.filter(
            (p) =>
              p.empresa.nombre
                .toLowerCase()
                .includes(this.filter.toLowerCase()) ||
              p.cobrador.nombre
                .toLowerCase()
                .includes(this.filter.toLowerCase())
          )
        : this.rendiciones;
    },
    rows() {
      return this.items.length;
    },
  },
  mounted() {
    this.misRendiciones().then(() => {
      this.loading = false;
    });
    this.estadoRendiciones().then(() => {
      this.loading = false;
    });
  },
  methods: {
    async verRecibos(rendicion) {
      const response = await ReciboService.getRecibosDeRendicion({
        idRendicion: rendicion.id,
      });
      this.recibos = response.data.data;
      this.$refs["modalRecibos"].show();
    },
    async estadoRendiciones() {
      try {
        const response = await RendicionService.getEstadosRendiciones();
        this.estados = response.data.data;
      } catch (err) {
        this.$bvToast.toast(`No se pudo obtener el estado de las rendiciones`, {
          title: err.response.data,
          toaster: "b-toaster-top-center",
          solid: true,
          variant: "danger",
        });
      }
    },
    getEstado(estado) {
      let tc = this.estados.find((t) => t.clave == estado);
      if (tc == null) {
        return "-";
      }
      return tc.descripcion;
    },
    getImporte(importe) {
      const options2 = { style: "currency", currency: "USD" };
      const numberFormat2 = new Intl.NumberFormat("en-US", options2);
      return numberFormat2.format(importe);
    },

    async misRendiciones() {
      try {
        const response = await RendicionService.getRendicionesDe({
          tipo: this.getGrupos[0],
          nombre: this.getUserId,
        });
        this.rendiciones = response.data.data;
      } catch (err) {
        this.$bvToast.toast(
          `No se pudo obtener el listado de rendiciones, actualiza la pagina o vuelva a intentarlo`,
          {
            title: err.response.data,
            toaster: "b-toaster-top-center",
            solid: true,
            variant: "danger",
          }
        );
      }
    },
    async rendirRendicion(rendicion) {
      try {
        const response = await RendicionService.rendir_rendicion({
          rendicion: rendicion.id,
        });
        this.$bvToast.toast(response.data.data[0].MENSAJE, {
          title: "Rendicion",
          toaster: "b-toaster-top-center succes",
          solid: true,
          variant: "success",
        });
        this.misRendiciones();
      } catch (err) {
        this.$bvToast.toast(
          `No se pudo rendir la rendicion, intente nuevamente`,
          {
            title: err.response,
            toaster: "b-toaster-top-center",
            solid: true,
            variant: "danger",
          }
        );
      }
    },
    descargarRendicionFormatPDF(rendicion) {
      var totEntregado = 0;
      var totRedondeo = 0;
      totEntregado =
        Number(rendicion.importe) - Number(rendicion.entrega.importecomision);
      totRedondeo = Number(rendicion.entrega.recibio) - Number(totEntregado);
      var fechaHoy = new Date();
      var fechaLetras = { year: "numeric", month: "long", day: "numeric" };
      var fechaDMY = moment.utc(String(rendicion.fecha)).format("DD/MM/YYYY");
      const doc = new jsPDF("p", "mm", [190, 700]);
      doc.setTextColor(100);
      doc.setFontSize(37);
      doc.text(20, 10, "Comprobante de Rendicion");
      doc.setTextColor(0);
      doc.setFontSize(22);
      doc.setFont("Calibri");
      doc.text(
        60,
        20,
        "Dolores, " + fechaHoy.toLocaleDateString("es-ES", fechaLetras)
      );
      doc.setFontSize(32);
      doc.text(5, 40, "Nro rendicion: " + rendicion.id);
      doc.text(5, 55, "Fecha Realizada: " + fechaDMY);
      doc.text(5, 70, "Recibi de: " + rendicion.cobrador.nombre);
      doc.text(5, 85, "Neto: " + this.getImporte(rendicion.importe));
      doc.text(
        5,
        100,
        "Comision: " + this.getImporte(rendicion.entrega.importecomision)
      );
      doc.text(5, 115, "Redondeo:$ " + totRedondeo);
      doc.text(5, 130, "Total Entregado:$ " + totEntregado);
      doc.text(
        5,
        145,
        "Recibido por el CAER: " + this.getImporte(rendicion.entrega.recibio)
      );
      doc.autoPrint();
      doc.output("dataurlnewwindow", { filename: "Comprobante-pago.pdf" });
    },
  },
};
</script>

