<script>
import { Bar } from "vue-chartjs";

export default {
  extends: Bar,
  props: {
    estadisticas: {
      type: Array,
      default: null,
    },
  },
  data() {
    return {
      opciones: {},
      data: {},
    };
  },
  methods: {
    armarData() {
      this.data = {
        id: 1,
        data: {
          labels: this.armarLabels(),
          datasets: [
            {
              label: false,
              data: this.armarDataLabel(),
              backgroundColor: this.armarColors(),
              borderColor: [],

              borderWidth: 1,
              fill: true,
            },
          ],
        },
      };
    },
    armarColors() {
      var coloresCobradores = [];
      this.estadisticas.map(function (obj) {
        coloresCobradores.push(obj.COLOR);
      });
      return coloresCobradores;
    },
    armarLabels() {
      var nombreCobradores = [];
      this.estadisticas.map(function (obj) {
        nombreCobradores.push(obj.COBRADOR);
      });
      return nombreCobradores;
    },
    armarDataLabel() {
      var montoCobradores = [];
      this.estadisticas.map(function (obj) {
        montoCobradores.push(obj.MONTO);
      });
      return montoCobradores;
    },
    setearOpciones() {
      this.opciones = {
        options: {
          responsive: true,
          maintainAspectRatio: false,
          legend: {
            display: false,
          },
          scales: {
            xAxes: [
              {
                gridLines: {
                  display: false,
                  color: "rgba(0, 0, 0, 0)",
                  drawOnChartArea: false,
                },
              },
            ],
            yAxes: [
              {
                display: false,
                gridLines: {
                  display: false,
                  color: "rgba(0, 0, 0, 0)",
                  drawOnChartArea: false,
                },
              },
            ],
          },
        },
      };
    },
  },
  mounted() {
    this.armarData();
    this.setearOpciones();
    this.renderChart(this.data.data, this.opciones.options);
  },
};
</script>
