import Api from '@/services/Api'
const API_USER = "";
const API_URL = "api/";

export default {

    getEmpresas(credentials) {
    
        return Api().get(API_URL + API_USER + "lista_empresas", credentials)
    },
    getFacturasEmpresa(credentials){        
        return Api().post(API_URL + API_USER + "facturas_empresa", credentials)
    
    },
  
}