import Api from '@/services/Api'
const API_USER = "";
const API_URL = "api/";

export default {

    esCobradorEmpresa(credentials) {    
        return Api().get(API_URL + API_USER + "iscobrador_empresa/"+ credentials.cobrador)
    },
    porcentajeComision(credentials) {    
        return Api().get(API_URL + API_USER + "porcentaje_comision/"+credentials.cobrador)
    }
}