<script>
import { Pie } from "vue-chartjs";

export default {
  extends: Pie,
  props: {
    estadisticas: {
      type: Array,
      default: null,
    },
  },
  data() {
    return {
      opciones: {},
      data: {},
      porcentajeCobrado:0,
      porcentajeNoCobrado:0
    };
  },
  methods: {
    armarData() {
      this.data = {
        id: 1,
        data: {
          labels: ['Cobrado '+this.porcentajeCobrado.toFixed(1)+'%', "No cobrado "+this.porcentajeNoCobrado.toFixed(1)+'%'],
          datasets: [
            {
              label: false,
              data: [
                this.estadisticas[0].COBRADO,
                this.estadisticas[0].SINCOBRAR,
              ],
              borderWidth: 1,
              backgroundColor: ["rgb(20, 20, 198)","rgb(248, 1, 1)"],
            },
          ],
        },
      };
    },

    setearOpciones() {
      this.opciones = {
        options: {
          responsive: true,
          maintainAspectRatio: false,
          legend: {
            display: true,
          },
        },
      };
    },
    calcularPorcentaje(){
      var total
        total= Number(this.estadisticas[0].COBRADO) + Number (this.estadisticas[0].SINCOBRAR)
        this.porcentajeCobrado =   (this.estadisticas[0].COBRADO *100) /total
        this.porcentajeNoCobrado= (this.estadisticas[0].SINCOBRAR *100) /total
    }
  },
  mounted() {
    this.calcularPorcentaje()
    this.armarData();
    this.setearOpciones();
    
    this.renderChart(this.data.data, this.opciones.options);
  },
};
</script>
