<template>
  <b-container class="listadoFacturas">
    <div v-if="loading" class="py-5 text-center box-shadow">
      <span class="text-danger">
        <b>Cargando</b>
      </span>
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
    </div>
    <div v-else>
      <b-row class="pb-2">
        <b-col md="8" sm="6" class="text-center pt-3">
          <div v-if="this.esEmpresa">
            <p class="h4 text-center">Facturas</p>
          </div>
          <div v-else>
            <p class="h4 text-center">Detalle recibos</p>
          </div>
        </b-col>
      </b-row>
      <b-table
        show-empty
        small
        responsive
        :busy.sync="isBusy"
        :items="facturas"
        :fields="fields"
        :filter="filter"
        :current-page="currentPage"
        :per-page="perPage"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
      >
        <template v-slot:cell(fecha)="row">{{row.item.fecha | formatDate }}</template>
        <template v-slot:cell(fechavencimiento1)="row">{{row.item.fechavencimiento1 | formatDate }}</template>
        <template v-slot:cell(fechavencimiento2)="row">{{row.item.fechavencimiento2 | formatDate }}</template>
        <template v-slot:cell(importe)="row">{{getImporte (row.item.importe)}}</template>
        <template v-slot:cell(importe2)="row">{{getImporte (row.item.importe2)}}</template>
        <template v-slot:cell(saldo)="row">{{getImporte (row.item.saldo)}}</template>
        <template
          slot="table-caption"
          v-if="this.facturas.length"
        >Mostrando {{this.rows}} de {{this.facturas.length}} entradas</template>
      </b-table>
      <b-row class="mt-auto">
        <b-col md="6" class="my-1">
          <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage" class="my-0"></b-pagination>
        </b-col>
        <b-col md="6" class="my-1">
          <b-row align-h="end">
            <b-col cols="5">
              <b-form-group label-cols="2" label="Ver" class="mb-0">
                <b-form-select v-model="perPage" :options="pageOptions"></b-form-select>
              </b-form-group>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </div>
  </b-container>
</template>    
<script>
import EmpresaService from "@/services/EmpresaService";
import { mapGetters } from "vuex";
export default {
  name: "ListadoFacturas",
   props: {
    esEmpresa: {
      type: Boolean,
      required: false,
      default: true
    },
     items: {
      type: Array,
       required: false
    },
    },
  data() {
    return {
      loading: true,
      isBusy: false,
      facturas: [],
      fields: [
        { key: "descripcion", label: "Concepto", class: "text-center p2" },
        { key: "nombre", label: "Persona", class: "text-left p2" },
        { key: "fecha", label: "Fecha", class: "text-left p2" },
        { key: "fechavencimiento1",label: "1° Vencimiento",class: "text-left p2",},
        { key: "fechavencimiento2",label: "2° Vencimiento",class: "text-left p2",},
        { key: "importe", label: "importe", class: "text-left p2" },
        { key: "importe2", label: "importe 2°", class: "text-left p2" }        
      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      sortBy: "",
      sortDesc: false,
      filter: null,
      sortDirection: "asc",
      pageOptions: [10, 15, 20],
      transProps: {
        name: "flip-list",
      },
    };
  },
  computed: {
    ...mapGetters("storeUser", ["getUserId", "getGrupos"]),    
    rows() {
      return this.facturas.length;
    },
  },
  created () {    
    if (this.esEmpresa){     
      this.facturasEmpresa().then(() => {
      this.loading = false;
    });
    }else{
      this.loading = false;
      this.facturas=this.items
      this.armarTablaFacturas()
    }
    
  },
  methods: {
     getImporte(importe) {       
      const options2 = { style: "currency", currency: "USD" };
      const numberFormat2 = new Intl.NumberFormat("en-US", options2);
      return numberFormat2.format(importe);
    },

    async facturasEmpresa() {
      try {
        const response = await EmpresaService.getFacturasEmpresa({
          empresa: this.getUserId,
        });
        this.facturas = response.data.data;
      } catch (err) {
        alert(err.response.data.data);
      }
    },
    armarTablaFacturas(){     
      this.fields= [
        { key: "descripcion", label: "Concepto", class: "text-center p2" },     
        { key: "fecha", label: "Fecha", class: "text-left p2" },  
        { key: "saldo", label: "importe Abonado", class: "text-left p2" },
        { key: "tipoPago", label: "Tipo de pago", class: "text-left p2" }   
      ]}  
  },
};
</script>

