<template>
  <b-container class="nuevaEntrega">
    <br />
    <div v-if="loading" class="py-5 text-center box-shadow">
      <span class="text-danger">
        <b>Cargando</b>
      </span>
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
    </div>
    <div v-else class="box-shadow p-4 mx-0">
      <b-row class="pb-3">
        <b-col lg="2" sm="4" class="text-center"></b-col>
        <b-col md="8" sm="6" class="text-left pt-3">
          <div v-if="this.items.length > 0">
            <p class="h4 text-center">Entrega de rendiciones</p>
          </div>
          <div v-else>
             <p class="h4 text-center">
            <b-alert show variant="success">Usted no posee entregas a realizar</b-alert>   
            </p>     
          </div>
        </b-col>
      </b-row>
      <b-container class="bv-example-row" v-if="this.items.length > 0">
        <b-row>
          <b-col cols="1" md="1">
            <p>Cobrador:</p>
          </b-col>
          <b-col cols="6" md="4">
            <b-form-select
              v-model="cobradorSeleccionado"
              size="sm"
              required
              :options="fCobradoresSelect"
            ></b-form-select>
          </b-col>
        </b-row>
      </b-container>
      <br>
      <b-col v-if="cobradorSeleccionado">
        <b-table
          class="flip-list-move"
          ref="selectableTable"
          small
          responsive
          :items="rendicionesDelCobrador()"
          :fields="fields"
          :tbody-transition-props="transProps"
          :current-page="currentPage"
          selectable
          :select-mode="selectMode"
          @row-selected="onRowSelected"
        >
          <div slot="table-busy" class="text-center text-danger my-2">
            <b-spinner class="align-middle"></b-spinner>
            <strong>Cargando...</strong>
          </div>
          <template v-slot:cell(selected)="{rowSelected}">
            <template v-if="rowSelected">
              <span aria-hidden="true">&check;</span>
              <span class="sr-only">{{selected}}</span>
            </template>
            <template v-else>
              <span aria-hidden="true">&nbsp;</span>
              <span class="sr-only">Not selected</span>
            </template>
          </template>
          <template v-slot:cell(importe)="row">{{getImporte (row.item.importe)}}</template>
        </b-table>
        <b-col class="text-right">
          <b-button
            type="button"
            variant="primary"
            @click="setEntrega(selected)"
            :disabled="selected.length == 0"
          >Entrega</b-button>
        </b-col>
      </b-col>
    </div>

    <div id="ModalRealizarEntrega">
      <b-modal size="lg" ref="ModalRealizarEntrega" hide-footer>
        <RealizarEntrega
          :entrega="this.entrega"
          :rendiciones="this.selected"
          @confirmarEntrega="confirmarEntrega"
        ></RealizarEntrega>
      </b-modal>
    </div>
  </b-container>
</template>

<script>
import RendicionService from "@/services/RendicionService";
import EntregasService from "@/services/EntregasService";
import RealizarEntrega from "@/components/entrega/RealizarEntrega.vue";

import { mapGetters } from "vuex";
export default {
  name: "nuevaEntrega",
  components: {
    RealizarEntrega
  },
  data() {
    return {
      entrega: {
        recibio: 1,
        fecha: new Date().toISOString().slice(0, 10),
        observaciones: "",
        nroComprobante: 0,
        saldo: null,
        importeComision: 0,
        cantrendiciones: 0,
        saldoTotalRendiciones: 0
      },
      loading: true,
      rendiciones: [],
      fields: [
        {
          key: "cobrador.nombre",
          label: "Cobrador",
          class: "text-left p2",
          sortable: true
        },
        { key: "fecha", label: "Fecha", class: "text-left p2", sortable: true },
        {
          key: "importe",
          label: "importe",
          class: "text-left p2",
          sortable: true
        },
        { key: "selected", label: "seleccionar", class: "text-left p2 " }
      ],
      currentPage: 1,
      transProps: {
        name: "flip-list"
      },
      selectMode: "single",
      selected: [],
      filter: null,
      cobradorSeleccionado: 0
    };
  },
  computed: {
    fCobradoresSelect() {
      let mc = this.items.map(e => ({
        value: e.cobrador.id,
        text: e.cobrador.nombre
      }));
      this.seleccionarPrimerCobrador(mc[0].value);
      mc = Array.from(new Set(mc.map(JSON.stringify))).map(JSON.parse);
      return mc;
    },
    ...mapGetters("storeUser", ["getUserId", "getGrupos"])
  },
  mounted() {
    this.misRendiciones().then(() => {
      this.loading = false;
    });
  },
  methods: {
    seleccionarPrimerCobrador(cobradorValor) {
      this.cobradorSeleccionado = cobradorValor;
    },
    rendicionesDelCobrador() {
      return this.items.filter(f => f.cobrador.id == this.cobradorSeleccionado);
    },
    onRowSelected(items) {
      this.selected = items;
    },
    setEntrega(rendiciones) {    
      this.limpiarCampos();
      this.entrega.cantrendiciones = rendiciones.length;
      this.calcularTotalRendiciones(rendiciones);
      this.entrega.nroComprobante= rendiciones[0].id
      this.entrega.saldo = this.entrega.saldoTotalRendiciones;
      this.$refs["ModalRealizarEntrega"].show();
    },
    calcularTotalRendiciones(rendiciones) {
      rendiciones.forEach(
        rendicion =>
          (this.entrega.saldoTotalRendiciones =
            Number(this.entrega.saldoTotalRendiciones) +
            Number(rendicion.importe))
      );
    },
    calcularSaldo() {
      this.calcularTotalRendiciones(this.selected);
      if (
        this.entrega.importeComision < this.totalRendiciones &&
        this.entrega.importeComision > 0
      ) {
        this.entrega.importeComision = Number.parseFloat(
          this.entrega.importeComision
        ).toFixed(2);
        this.totalRendiciones =
          Number(this.totalRendiciones) - Number(this.entrega.importeComision);
        this.totalRendiciones = Number.parseFloat(
          this.totalRendiciones
        ).toFixed(2);
      }
    },
    limpiarCampos() {
      (this.entrega.recibio = null),
        (this.entrega.fecha = new Date().toISOString().slice(0, 10)),
        (this.entrega.observaciones = null),
        (this.entrega.nroComprobante = 0),
        (this.entrega.cantrendiciones = 0),
        (this.entrega.saldo = null),
        (this.entrega.importeComision = 0);
      this.entrega.saldoTotalRendiciones = 0;
    },
    async confirmarEntrega() {
      this.$refs["ModalRealizarEntrega"].hide();
      try {
        const response = await EntregasService.addEntregaRendiciones({
          recibio: this.entrega.recibio,
          fecha: this.entrega.fecha,
          observaciones: this.entrega.observaciones,
          nroComprobante: this.entrega.nroComprobante,
          saldo: this.entrega.saldo,
          comision: this.entrega.importeComision,
          rendiciones: this.selected
        });
        this.$root.$bvToast.toast(`Entrega generada `, {
          title: response.data.data,
          toaster: "b-toaster-top-center succes",
          solid: true,
          variant: "success"
        });
        this.$router.push({ name: "ListadoRendiciones" });
      } catch (error) {
        this.$bvToast.toast(`No se pudo generar La entrega`, {
          title: error.response.data.data,
          toaster: "b-toaster-top-center",
          solid: true,
          variant: "danger"
        });
      }
      //this.misRendiciones();
    },

    async misRendiciones() {
      try {
        const response = await RendicionService.getRendicionesDe({
          tipo: this.getGrupos[0],
          nombre: this.getUserId
        });        
        this.rendiciones = response.data.data.filter(r => r.entrega == null  || r.estado == '01');
        this.items = response.data.data.filter(r => r.entrega == null || r.estado == '01');
      } catch (err) {
        this.$bvToast.toast(`No se pudo obtener las rendiciones`, {
          title: err.response,
          toaster: "b-toaster-top-center",
          solid: true,
          variant: "danger"
        });
      }
    },
    getImporte(importe) {
      const options2 = { style: "currency", currency: "USD" };
      const numberFormat2 = new Intl.NumberFormat("en-US", options2);
      return numberFormat2.format(importe);
    }
  }
};
</script>

