<template>
  <b-container class="listadoRecibos">
    <div v-if="loading" class="py-5 text-center box-shadow">
      <span class="text-danger">
        <b>Cargando</b>
      </span>
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
    </div>
    <div v-else>
      <b-row class="pb-3">
        <b-col lg="2" sm="4" class="text-center"></b-col>
        <b-col md="8" sm="6" class="text-left pt-3">
          <div>
            <p class="h4 text-center" v-if="!verFacturasRecibos">Recibos</p>
          </div>
        </b-col>
      </b-row>
      <b-table
        show-empty
        small
        responsive
        :busy.sync="isBusy"
        :items="items"
        :fields="fields"
        :filter="filter"
        :current-page="currentPage"
        :per-page="perPage"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
      >
        <template v-if="verFacturasRecibos" v-slot:cell(fecha)="row" >{{row.item.fecha | formatDate }}</template>
        <template v-slot:cell(importe)="row">{{getImporte (row.item.importe)}}</template>
        <template v-slot:cell(acciones)="row">
          <b-button type="button" @click="verFacturas(row.item)" variant="outline-primary">
            <b-icon icon="file-earmark-text-fill"></b-icon>
          </b-button>
        </template>
        <template
          slot="table-caption"
          v-if="this.items.length"
        >Mostrando {{this.rows}} de {{this.items.length}} entradas</template>
      </b-table>
      <b-row class="mt-auto">
        <b-col md="6" class="my-1">
          <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage" class="my-0"></b-pagination>
        </b-col>
        <b-col md="6" class="my-1">
          <b-row align-h="end">
            <b-col cols="5">
              <b-form-group label-cols="2" label="Ver" class="mb-0">
                <b-form-select v-model="perPage" :options="pageOptions"></b-form-select>
              </b-form-group>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </div>
    <div id="modalFacturas">
      <b-modal size="lg" ref="modalFacturas" ok-only>
        <ListadoFacturas 
            :items="facturas"
            :esEmpresa='false'
            ></ListadoFacturas>
            
      </b-modal>
    </div>
  </b-container>
</template>   

<script>
import ListadoFacturas from "@/components/facturas/ListadoFacturas.vue";
export default {
  components: {
    ListadoFacturas,
  },
  props: {
    items: {
      type: Array,
    },
    verFacturasRecibos: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  name: "ListadoRecibos",
  data() {
    return {
      loading: false,
      isBusy: false,
      facturas: [],
      fields: [
        { key: "nombre", label: "Usuario", class: "text-left", sortable: true },
        { key: "fecha", label: "Fecha", class: "text-left", sortable: true },
        { key: "importe",label: "importe",class: "text-left",sortable: true,},
      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      sortBy: "",
      sortDesc: false,
      filter: null,
      sortDirection: "asc",
      pageOptions: [10, 15, 20],
      transProps: {
        name: "flip-list",
      },
    };
  },
  created() {
    if (this.verFacturasRecibos == true) {
      this.armarTablaConFacturas();
    }
  },
  computed: {
    rows() {
      return this.items.length;
    },
  },

  methods: {
    getImporte(importe) {
      const options2 = { style: "currency", currency: "USD" };
      const numberFormat2 = new Intl.NumberFormat("en-US", options2);
      return numberFormat2.format(importe);
    },
    armarTablaConFacturas() {      
      this.fields = [
        { key: "nombre",label: "Cobrador",class: "text-left",sortable: true,},
        { key: "fecha", label: "Fecha Abonado", class: "text-left", sortable: true },
        { key: "importe",label: "importe Abonado",class: "text-left",sortable: true,},
        { key: "acciones",label: "Acciones",class: "text-left",sortable: true,},
      ];
    },
    verFacturas(recibo) {       
      recibo.facturas.forEach(factura => {        
        this.calcularTipoPagoFacturas(factura,recibo.fecha)
      });
      this.facturas =  recibo.facturas;
      this.$refs["modalFacturas"].show();
    },
    calcularTipoPagoFacturas(factura,fecha){
      if (fecha <= factura.fechavencimiento1){
          factura.tipoPago =factura.saldo >= factura.importe ? 'TOTAL' : 'PARCIAL'
        }else{
          factura.tipoPago =factura.importe2 >= factura.importe ? 'TOTAL' : 'PARCIAL'
        }
    }
  },
};
</script>

