import Api from '@/services/Api'
const API_USER = "";
const API_URL = "api/";

export default {

    getFacturas(credentials) {
    
        return Api().get(API_URL + API_USER + "facturas", credentials)
    },
    addFactura(credentials) {    
        return Api().post(API_URL + API_USER + "addfactura", credentials)
    },
    getFacturasdeCliente(credentials) {    
        return Api().post(API_URL + API_USER + "facturas_cliente", credentials)
    },
    getPersonas(credentials) {    
        return Api().post(API_URL + API_USER + "getPersonas", credentials)
    },


}