<template>
  <b-container class="Estadisticas">
    <br />
    <div v-if="loading" class="py-5 text-center box-shadow">
      <span class="text-danger">
        <b>Cargando</b>
      </span>
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
    </div>
    <div v-else>
      <b-card-group deck class="mb-3">
        <b-card
          no-body
          border-variant="secondary"
          header="Cobros por cobrador"
          header-border-variant="secondary"
          align="center"
        >
          <b-card-text>
            <p>Datos de los últimos 30 dias</p>
            <p>
              Total Cobrado:
              {{ this.calcularTotal(this.estadisticasCobradores) }}
            </p>
            <GraficoBar
              :estadisticas="this.estadisticasCobradores"
              class="h-75"
            />
          </b-card-text>
        </b-card>
        <b-card
          no-body
          border-variant="secondary"
          header="Cobros por fecha"
          header-border-variant="secondary"
          align="center"
        >
          <b-card-text>
            <p>Datos de los últimos 30 dias</p>
            <p>Total Cobrado: {{ this.calcularTotalCobradoPorFecha() }}</p>
            <GraficoLine
              :estadisticas="this.estadisticasPorFecha"
              class="h-75"
            />
          </b-card-text>
        </b-card>
      </b-card-group>
      <b-card-group deck lass="mt-4">
        <b-card
          no-body
          border-variant="secondary"
          header="Cobros sin rendir"
          header-border-variant="secondary"
          align="center"
        >
          <b-card-text>
            <p></p>
            <p>
              Total Cobrado:
              {{ this.calcularTotal(this.estadisticasRendicion) }}
            </p>
            <GraficoBar
              :estadisticas="this.estadisticasRendicion"
              class="h-75"
            />
          </b-card-text>
        </b-card>
        <b-card
          no-body
          border-variant="secondary"
          header="Porcentaje Cobrado"
          header-border-variant="secondary"
          align="center"
        >
          <b-card-text>
            <p></p>
            <GraficoTorta
              id="graficoTorta"
              :estadisticas="this.estadisticasPorcentajeCobrado"
            />
          </b-card-text>
        </b-card>
      </b-card-group>
    </div>
  </b-container>
</template>

<script>
import axios from "axios";
import EstadisticasService from "@/services/EstadisticasService";
import { mapGetters } from "vuex";
import GraficoBar from "@/components/estadisticas/GraficoBar.vue";
import GraficoTorta from "@/components/estadisticas/GraficoTorta.vue";
import GraficoLine from "@/components/estadisticas/GraficoLine.vue";
export default {
  name: "estadisticas",
  components: {
    GraficoBar,
    GraficoTorta,
    GraficoLine,
  },
  data() {
    return {
      loading: true,
      estadisticasRendicion: {},
      estadisticasCobradores: {},
      estadisticasPorcentajeCobrado: {},
      estadisticasPorFecha: {},
    };
  },
  computed: {
    ...mapGetters("storeUser", ["getUserId"]),
  },
  methods: {
    async getEstadisticasCobradores() {
      try {
        const response = await EstadisticasService.getEstadisticasCobradores({
          empresa: this.getUserId,
        });
        this.estadisticasCobradores = response.data.data;
      } catch (error) {
        this.$bvToast.toast(`No se pudo obtener la estadistica`, {
          title: error.response.data.data,
          toaster: "b-toaster-top-center",
          solid: true,
          variant: "danger",
        });
      }
    },

    async getEstadisticasSinRendicion() {
      try {
        const response = await EstadisticasService.getEstadisticasSinRendicion({
          empresa: this.getUserId,
        });
        this.estadisticasRendicion = response.data.data;
      } catch (error) {
        this.$bvToast.toast(`No se pudo obtener la estadistica`, {
          title: error.response.data.data,
          toaster: "b-toaster-top-center",
          solid: true,
          variant: "danger",
        });
      }
    },
    async getEstadisticasPorcentajeCobrado() {
      try {
        const response = await EstadisticasService.getEstadisticasPorcentajeCobrado(
          {
            empresa: this.getUserId,
          }
        );
        this.estadisticasPorcentajeCobrado = response.data.data;
      } catch (error) {
        this.$bvToast.toast(`No se pudo obtener la estadistica`, {
          title: error.response.data.data,
          toaster: "b-toaster-top-center",
          solid: true,
          variant: "danger",
        });
      }
    },
    async getEstadisticasCobradoPorFecha() {
      try {
        const response = await EstadisticasService.getEstadisticasCobradoPorFecha(
          {
            empresa: this.getUserId,
          }
        );
        this.estadisticasPorFecha = response.data.data;
      } catch (error) {
        this.$bvToast.toast(`No se pudo obtener la estadistica`, {
          title: error.response.data.data,
          toaster: "b-toaster-top-center",
          solid: true,
          variant: "danger",
        });
      }
    },
    calcularTotal(cobradores) {
      var montoTotal = 0;
      cobradores.forEach((cobrador) => {
        montoTotal = Number(montoTotal) + Number(cobrador.MONTO);
      });
      return this.getImporte(montoTotal);
    },

    calcularTotalCobradoPorFecha() {
      var montoTotal = 0;
      this.estadisticasPorFecha.forEach((cobrado) => {
        montoTotal = Number(montoTotal) + Number(cobrado.Monto);
      });
      return this.getImporte(montoTotal);
    },
    getImporte(importe) {
      const options2 = { style: "currency", currency: "USD" };
      const numberFormat2 = new Intl.NumberFormat("en-US", options2);
      return numberFormat2.format(importe);
    },
  },
  mounted() {
    axios
      .all([
        this.getEstadisticasSinRendicion(),
        this.getEstadisticasCobradores(),
        this.getEstadisticasPorcentajeCobrado(),
        this.getEstadisticasCobradoPorFecha(),
      ])
      .then(() => {
        this.loading = false;
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>

<style>
#graficoTorta {
  display: block;
  width: 496px;
  height: 274px;
}
</style>