                        >
                            <ValidationProvider name="fecha " :rules="'required'" v-slot="{ errors,valid }">

                                <b-form-input 
                                    v-model="factura.fecha"                                     
                                    :state="errors[0] ? false : (valid ? true : null)"                                    
                                    size="sm"
                                    type="date"                                   
                                    >
                                </b-form-input>
                                <b-form-invalid-feedback v-for="error in errors " :key="error.key" >
                                {{ error }}
                                </b-form-invalid-feedback>
                            </ValidationProvider>
                        </b-form-group>

                    </b-col>
               </b-row>    
          <b-row>
             <b-col lg="5" md="6" >
                        <b-form-group
                            id="fechaVencimiento-label"
                            label="Fecha 1° Vencimiento"
                            label-for="fechaVencimiento"                        
                        >
                            <ValidationProvider name="fecha Vencimiento" :rules="'required'" v-slot="{ errors,valid }">

                                <b-form-input 
                                    v-model="factura.fechaVencimiento"                                     
                                    :state="errors[0] ? false : (valid ? true : null)"                                    
                                    size="sm"
                                    type="date"                                   
                                    >
                                </b-form-input>
                                <b-form-invalid-feedback v-for="error in errors " :key="error.key" >
                                {{ error }}
                                </b-form-invalid-feedback>
                            </ValidationProvider>
                        </b-form-group>

                    </b-col>

                   <b-col lg="5" md="6" >
                        <b-form-group
                            id="fechaVto-label"
                            label="Fecha 2° vencimiento  "
                            label-for="fechaVto2"                        
                        >
                            <ValidationProvider name="fecha vencimiento 2 " :rules="'required'" v-slot="{ errors,valid }">
                                <b-form-input 
                                    v-model="factura.fechaVencimiento2"                                     
                                  :state="errors[0] ? false : (valid ? true : null)"
                                    size="sm"
                                    type="date"                                   
                                    >
                                </b-form-input>
                                <b-form-invalid-feedback v-for="error in errors " :key="error.key" >
                                {{ error }}
                                </b-form-invalid-feedback>
                            </ValidationProvider>
                        </b-form-group>

                    </b-col>
              </b-row>
            <b-row>
              <b-col lg="5" md="6">
                 <b-form-group
                            id="importe-label"
                            label="Importe:"
                            label-for="importe"          
                        >          
                    <ValidationProvider :name="'importe '" :rules="'required'" v-slot="{ errors,valid  }">
                       <b-form-input                     
                     size="sm"     
                     type="number"
                    v-model="factura.importe"      
                    :state="errors[0] ? false : (valid ? true : null)"
                     ></b-form-input>
                    <b-form-invalid-feedback v-for="error in errors " :key="error.key" >
                    {{ error }}
                    </b-form-invalid-feedback>
                       </ValidationProvider>
                 </b-form-group>  
            </b-col>
              <b-col lg="5" md="6">
                 <b-form-group
                            id="importe2-label"
                            label="Importe 2°:"
                            label-for="importe2"          
                        >          
                    <ValidationProvider :name="'importe2 '" :rules="'required'" v-slot="{ errors,valid  }">
                       <b-form-input                     
                     size="sm"     
                    v-model="factura.importe2"     
                     type="number" 
                    :state="errors[0] ? false : (valid ? true : null)"
                     ></b-form-input>
