                        <b-form-group class="mb-0" id="observaciones-label" label="Observaciones" label-for="observaciones">
                            <b-form-textarea
                                id="observaciones"
                                size="sm"
                                rows="1"                              
                                v-model="factura.observaciones"
                                placeholder="Observaciones"
                            ></b-form-textarea>
                        </b-form-group>
                    </b-col>
            </b-row>  
        </b-card>
          <b-card  class="mb-3"
