       </b-card>

      


          </b-container>       
 </ValidationObserver>         
          <b-row>
             <b-col class="text-left"> 
                   <b-button  @click="volver()"  variant="outline-secondary">                                       
                       <b-icon icon="arrow-left-short"></b-icon>    Volver 
                     </b-button>    
                </b-col>  
                 <b-col class="text-right">
                      <div  v-if="!creando">
                    <b-button  type="button" @click="setFactura()"  :disabled="creando" variant="primary">                       
                                Nueva                                 
                    </b-button>  
                    </div>  
                    <div v-else >
                        <b-spinner variant="primary" label="Text Centered"></b-spinner>
                   </div>                   
                </b-col>
          </b-row> 
    </div>
   </b-container>   
</template>


<script>
import FacturaService  from '@/services/FacturaService'
import { mapGetters } from 'vuex'
export default {
    name:"nuevaFactura",
   
     data() {
    return {
        factura: {        
        importe : null,
        importe2:null,
        fecha:new Date().toISOString().slice(0,10),
        fechaVencimiento:null,
        fechaVencimiento2:null, 
        observaciones:null  
      },         
         alerts: [],
         loading: false,  
         creando:false,      
          persona: {                
            dni: '',
            nombre:'',                
            error: '',
            direccion:''
           },             
    } 
    },
    created(){     
   
    },
    computed:{
           
          ...mapGetters ('storeUser',['getUserId']),

    },
    mounted () {     
        
      
    },
  methods: {
          volver(){     
             this.$router.push({name:'ListaFacturasEmpresa'})                 
          },
         async setFactura(){
                const valid = await  this.validate();                 
            if (valid) { 
                this.creando=true  
                 try {                     
                  const response = await FacturaService.addFactura ({   
                    empresaId: this.getUserId,          
                    importe:this.factura.importe,  
                    importe2:this.factura.importe2,
                    fecha : this.factura.fecha,
                    fechaVencimiento:this.factura.fechaVencimiento,
                    fechaVencimiento2:this.factura.fechaVencimiento2, 
                    descripcion : this.factura.observaciones,                   
                    dni: this.persona.dni,
                     nombre:this.persona.nombre,  
                     domicilio:this.persona.direccion
                    }) 
                     this.$bvToast.toast(`Factura generada `, {
                              title: response.data.data,
                              toaster: 'b-toaster-top-center succes',
                              solid: true,
                              variant: "success",                            
                    })                   
                     this.creando= response.data.error          
                    }catch (error){          
                      error.response.data.data.forEach ((data) => {
                              this.$bvToast.toast(`No se pudo generar La Factura`, {
                              title: data,
                              toaster: 'b-toaster-top-center',
                              solid: true,
                              variant: "danger",
                            
                          })         
                           }) 
                     }
                  
            }
            
            
          },
         async validate () { 
          if (this.loading ) {
            return false 
          }else {  
            let result = await this.$refs.nuevaFactura.validate();   
            return result
           }
        },
   }
}




</script>

