<template>
  <b-container>
    
    <b-col lg="4" md="4">
      <b-form-select v-model="empresaSeleccionada" size="sm" required :options="fEmpresasSelect"></b-form-select>
    </b-col>
    <b-col v-if="empresaSeleccionada">
      <ValidationObserver ref="nuevoPago">
        <b-col class="text-right">
          <b-form-group
            label-cols="8"
            label-cols-lg="10"
            id="adedudado-label"
            label="Total $"
            label-for="adeudado"
          >
            <vue-numeric
              style="width:123px;text-align:right"
              v-model="totalPagar"
              disabled
              separator="."
              :min="0"
              :precision="2"
              :max="999999"
            ></vue-numeric>
          </b-form-group>
          <b-form-group
            label-cols="8"
            label-cols-lg="10"
            label="A pagar $"
            label-for="input-horizontal"
          >
            <vue-numeric             
              v-on:input="actualizarVuelto"
              style="width: 123px;text-align:right"
              separator="."
              :min="0"
              :precision="2"
              v-model="entrega"
            ></vue-numeric>
          </b-form-group>
          <b-form-group
            label-cols="8"
            label-cols-lg="10"
            label="Entrega $"
            label-for="input-horizontal"
          >
            <ValidationProvider :name="'Entrega '" :rules="'between:'+entrega +',' +max">
              <vue-numeric
               v-focus
                style="width: 123px;text-align:right"
                v-model="entregaEnMano"
                v-on:input="actualizarVuelto"
                separator="."
                :placeholder="'0,00'"
                :precision="2"
              ></vue-numeric>
            </ValidationProvider>
            <p
              v-if="montoEntregaInvalido"
              style="color: red;font-size: 10px;"
            >el monto debe ser mayor</p>
          </b-form-group>
          <b-form-group
            label-cols="8"
            label-cols-lg="10"
            label="Vuelto $"
            label-for="input-horizontal"
          >
            <vue-numeric
              style="width: 123px;text-align:right"
              separator="."
              disabled
              :min="0"
              :precision="2"
              v-model="vuelto"
            ></vue-numeric>
          </b-form-group>
        </b-col>
      </ValidationObserver>
      <!-- TABLA  -->
      <b-button style="margin: 5px" size="sm" @click="selectAllRows">Todas</b-button>
      <b-button size="sm" @click="clearSelected">Borrar</b-button>
      <b-table
        class="flip-list-move"
        ref="selectableTable"
        small
        responsive
        :items="facturasEmpresa()"
        :fields="fields"
        :tbody-transition-props="transProps"
        :current-page="currentPage"
        selectable
        :select-mode="selectMode"
        @row-selected="onRowSelected"
      >
        <div slot="table-busy" class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
          <strong>Cargando...</strong>
        </div>
        <template v-slot:cell(selected)="{rowSelected}">
          <template v-if="rowSelected">
            <span aria-hidden="true">&check;</span>
            <span class="sr-only">{{selected}}</span>
          </template>
          <template v-else>
            <span aria-hidden="true">&nbsp;</span>
            <span class="sr-only">Not selected</span>
          </template>
        </template>
        <template v-slot:cell(importe)="row">{{getImporte (row.item.importe)}}</template>
        <template v-slot:cell(importe2)="row">{{getImporte (row.item.importe2)}}</template>
        <template v-slot:cell(debe)="row">{{getImporte (row.item.debe)}}</template>
        <template v-slot:cell(fecha)="row">{{row.item.fecha | formatDate }}</template>
        <template v-slot:cell(fechavencimiento1)="row">{{row.item.fechavencimiento1 | formatDate }}</template>
        <template v-slot:cell(fechavencimiento2)="row">{{row.item.fechavencimiento2 | formatDate }}</template>
      </b-table>

      <div>
        <b-col class="text-right">
          <b-button
            type="button"
            @click="setRecibo(selected )"
            :disabled="selected.length == 0"
            variant="primary"
            @keyup.enter="setRecibo(selected)"
          >
            <b-icon
              icon="arrow-counterclockwise"
              v-if="creando"
              animation="spin-reverse-pulse"
              font-scale="2"
            ></b-icon>Recibo
          </b-button>
        </b-col>
      </div>
    </b-col>

    <div id="modalCobro">
      <b-modal ref="modalCobro" title="Detalle del pago" hide-footer>
        <DetallePago @okReciboModal="okReciboModal" :selected="this.selected"></DetallePago>
      </b-modal>
    </div>
  </b-container>
</template>
<script>
import DetallePago from "@/components/facturas/DetallePago.vue";
export default {
  name: "listaFacturas",
  components: {
    DetallePago
  },
  props: {
    items: {
      type: Array
    }
  },
  data() {
    return {
      loading: false,
      facturas: [],
      creando: false,
      isBusy: false,
      fields: [
        { key: "descripcion", label: "Factura", class: "text-center " },
        { key: "fecha", label: "Fecha", class: "text-left " },
        {
          key: "fechavencimiento1",
          label: "1° Vencimiento",
          class: "text-left "
        },
        {
          key: "fechavencimiento2",
          label: "2° Vencimiento",
          class: "text-left "
        },
        { key: "importe", label: "importe", class: "text-right " },
        { key: "importe2", label: "importe 2°", class: "text-right " },
        { key: "debe", label: "Debe", class: "text-right " },
        { key: "selected", label: "Pagar", class: "text-center " }
      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, 20],
      transProps: {
        name: "flip-list"
      },
      selectMode: "multi",
      selected: [],
      filter: null,
      totalPagar: 0,
      entrega: 0,
      entregaEnMano: 0,
      vuelto: 0,
      empresaSeleccionada: 0,
      max: 9999999,
      montoEntregaInvalido: false
    };
  },
  computed: {
    fEmpresasSelect() {
      let mc = this.items.map(e => ({
        value: e.empresa,
        text: e.nombreempresa
      }));
      this.seleccionarPrimerFactura(mc[0].value);
      mc = Array.from(new Set(mc.map(JSON.stringify))).map(JSON.parse);
      return mc;
    }
  },
  directives: {
    focus: {
      inserted: function(el) {
        el.focus();
      }
    }
  },

  methods: {
    seleccionarPrimerFactura(valorFactura) {
      this.empresaSeleccionada = valorFactura;
    },

    clearSelected() {
      this.$refs.selectableTable.clearSelected();
      this.vuelto = 0;
      this.entregaEnMano = 0;
      this.montoEntregaInvalido = false;
    },
    getImporte(importe) {
      const options2 = { style: "currency", currency: "USD" };
      const numberFormat2 = new Intl.NumberFormat("en-US", options2);
      return numberFormat2.format(importe);
    },
    facturasEmpresa() {
      return this.items.filter(f => f.empresa == this.empresaSeleccionada);
    },
    onRowSelected(items) {
      this.totalPagar = this.calcularImporteTotal(items);
      this.entrega = this.calcularImporteTotal(items);
      this.entregaEnMano > 0
        ? (this.vuelto = Number(this.entregaEnMano) - Number(this.entrega))
        : (this.vuelto = 0);
      this.selected = items;
    },

    calcularImporteTotal(facturas) {
      var fechaHoy=  new Date(); fechaHoy.setHours(0,0,0,0)    
      var importeTotal = 0;
      var importeFactura = 0;
      facturas.forEach(function(factura) {
        var fechaFactura = new Date(factura.fechavencimiento1)
        fechaFactura.setHours(0,0,0,0)      
        importeFactura =
         fechaHoy.getTime() <= fechaFactura.getTime()
            ? factura.importe
            : factura.importe2;
        importeFactura = Number(importeFactura) - Number(factura.saldo);
        importeTotal = Number(importeTotal) + Number(importeFactura);
      });
      return Number(importeTotal);
    },
    selectAllRows() {
      this.$refs.selectableTable.selectAllRows();
    },
    async setRecibo(facturasSeleccionadas) {
      const valid = await this.$refs.nuevoPago.validate();
      if (valid) {
        this.realizarPagoParcialFacturas(facturasSeleccionadas, this.entrega);
        if (this.totalPagar != this.entrega) {
          this.$refs["modalCobro"].show();
        } else {
          this.$router.push({
            name: "NuevoRecibo",
            params: {
              facturas: facturasSeleccionadas,
              totalPagar: this.totalPagar,
              entrega: this.entrega
            }
          });
        }
      } else {
        this.montoEntregaInvalido = true;
      }
    },

    realizarPagoParcialFacturas(facturas, entrega) {
      var fechaHoy=  new Date(); fechaHoy.setHours(0,0,0,0)    
      var importeFactura = 0;
      facturas.forEach(function(factura) {
        var fechaFactura = new Date(factura.fechavencimiento1)
        fechaFactura.setHours(0,0,0,0) 
        importeFactura =
          fechaHoy.getTime() <= fechaFactura.getTime()
            ? factura.importe
            : factura.importe2;
        importeFactura = Number(importeFactura) - Number(factura.saldo);
        if (entrega >= factura.debe) {
          factura.pagada = true;
          factura.debe = 0;
          factura.saldo = Number(importeFactura) + Number(factura.saldo);
          entrega = Number(entrega) - Number(importeFactura);
        } else {
          factura.pagada = false;
          factura.debe = Number(factura.debe) - Number(entrega);
          factura.saldo = Number(entrega) + Number(factura.saldo);
          entrega = 0;
        }
      });
    },
    okReciboModal() {
      this.$router.push({
        name: "NuevoRecibo",
        params: {
          facturas: this.selected,
          totalPagar: this.totalPagar,
          entrega: this.entrega
        }
      });
    },
    actualizarVuelto() {
      this.montoEntregaInvalido = false;
      this.vuelto = Number(this.entregaEnMano) - Number(this.entrega);
    }
  },
  mounted() {
    this.selectAllRows();
  }
};
</script>

