<template>
  <b-container class="HistorialRecibosPagados">  
    <div v-if="loading" class="py-5 text-center box-shadow">
      <span class="text-danger">
        <b>Cargando</b>
      </span>
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
    </div>
    <div v-else class="box-shadow p-2 mx-1">
      <b-row class="pb-3">
        <b-col lg="2" sm="4" class="text-center"></b-col>
        <b-col md="8" sm="6" class="text-left pt-3">
          <div>
            <p class="h4 text-center">Historial de recibos pagos</p>
          </div>
        </b-col>
      </b-row>
      <ValidationObserver ref="buscarRecibos">
        <b-container class="pb-3">
          <b-form-group>
            <b-alert
              v-for="alert in alerts "
              dismissible
              v-bind:key="alert.key"
              show
              :variant="alert.variant"
            >{{alert.message}}</b-alert>
          </b-form-group>
          <b-card class="mb-3" header="Persona a buscar">
            <b-row>
              <b-col sm="4" lg="3">              
                <b-form-group                
                  id="DNI-label"
                  label="DNI-Socio:"
                  label-for="DNI"
                ></b-form-group>
                <ValidationProvider
                  :name="'Persona '"
                  :rules="'required|max:10'"
                  v-slot="{ errors , valid }"
                >
                  <b-form-input
                    v-focus
                    id="DNI-nroSocio"
                    type="number"
                    v-model="persona.dniOsocio"
                    @click="vaciarCampos()"
                    v-hotkey="{ enter: checkPersona}"
                    size="sm"
                    required
                    :state="(errors[0] || persona.error) ? false : (valid ? true : null)"
                    placeholder="persona a buscar"
                  ></b-form-input>
                  <b-form-invalid-feedback v-for="error in errors " :key="error.key">{{ error }}</b-form-invalid-feedback>
                  <b-form-invalid-feedback v-if="persona.error">{{persona.error}}</b-form-invalid-feedback>
                </ValidationProvider>
              </b-col>
              <b-col sm="8" lg="9" class="mt-sm-2 pt-sm-4">
                <div v-if="!buscandoPersona">
                  <b-button class="mr-2" variant="primary" size="sm" @click="checkPersona()">Buscar</b-button>
                  <span v-if="persona.nombreYapellido">
                    <span class="font-weight-bold">Nombre y Apellido:</span>
                    {{persona.nombreYapellido }}
                  </span>
                </div>
                <div v-else>
                  <b-spinner variant="primary" label="Text Centered"></b-spinner>
                </div>
              </b-col>
            </b-row>
          </b-card>

          <b-col v-if="this.message">
            <b-alert show variant="primary">{{message}}</b-alert>
          </b-col>

          <b-card class="mb-3" header="Recibos pagados" v-if="this.recibos.length  > 0">
            <b-row>
          <ListadoRecibos 
                 :items="recibos"
                 :verFacturasRecibos="true"
          ></ListadoRecibos>
            </b-row>
          </b-card>
        </b-container>
      </ValidationObserver>
    </div>
  </b-container>
</template>


<script>
import ListadoRecibos from "@/components/recibo/ListadoRecibos.vue";
import ReciboService from "@/services/ReciboService";
export default {
  name: "listadoReciboPagados",
  components: {
     ListadoRecibos,
  },
  data() {
    return {
      recibos: [],
     recibosPersona: 0,
      message: "",
      alerts: [],
      buscandoPersona: false,
      loading: false,      
      persona: {  
        nombreYapellido: "",
        dniOsocio: "",
        error: "",
      },
    };
  },
  directives: {
    focus: {
      // Definición de directiva
      inserted: function (el) {
        el.focus();
      },
    },
  },
  computed: {  
  },
  methods: {
    vaciarCampos() {
      this.persona.nombreYapellido = "";   
      this.recibos = [];
      this.message = "";
      this.persona.dniOsocio = "";
    },
    async checkPersona() {
      try {
        this.buscandoPersona = true;
        const response = await ReciboService.getRecibosPagadosdeCliente({          
          dniOsocio: this.persona.dniOsocio,
        }) 
       
        this.recibos = response.data.data;
      } catch (err) {
        this.$bvToast.toast(err.response.data.message, {
              title: 'Atencion!',
              toaster: "b-toaster-top-center",
              solid: true,
              variant: "danger"
            });       
      }
      this.buscandoPersona = false;      
      if (this.recibos.length > 0) {
        this.message = "";      
        this.completarDatosPersona(this.recibos[0])  
      } else {
        this.message = "La persona no posee recibos pagados";
      }
      
    },  
    completarDatosPersona(recibo){       
        this.persona.nombreYapellido= recibo.facturas[0].nombre
    }
     
  },
  mounted() {  
  },
};
</script>

