<template>
    <b-container class="Novedades">
    <br>
 <div  v-if="loading"  class="py-5 text-center box-shadow" >      
      <span class="text-danger "> <b> Cargando</b></span>
        <b-spinner variant="primary" label="Text Centered"></b-spinner>
  </div>
<div  v-else class="box-shadow p-2 mx-1">      
      <b-row class="pb-3">
        <b-col lg="2" sm="4" class=" text-center " >           
        </b-col>        
        <b-col  md="8" sm="6" class="text-left pt-3">  
           <div >
            <p class="h4 text-center">Novedad</p>
          </div>             
        </b-col>      
      </b-row>     
 <transition name="fade"  v-for="novedad in novedades" v-bind:key="novedad.key" >
        <div class="card mb-3" >
            <div class="card-header">
                <h5>{{novedad.titulo}}</h5>
            </div>
          <div class="card-body">
                <p class="card-text">
                    <ul>
                        <li>
                            {{ novedad.descripcion }} 
                            <b-button v-for="link in novedad.links" class="mx-1" v-bind:key="link.key" size="sm" :href="link.link" target="blank" :variant="link.variant || 'secondary'" > 
                                {{link.descripcion}}
                            </b-button>
                        </li>
                    </ul>
                </p>
            </div>
        </div>
        </transition>
 </div>  
</b-container>
  </template>
<script>
import NovedadesService from '@/services/NovedadesService'; 
export default {
  name:"novedades",
   
  data() {
      return {
       novedades:[],
       loading:true
      }
   },
    methods: {  
                async getNovedades() {
                        try {                  
                            const response = await NovedadesService.getNovedades()                        
                            this.novedades = response.data.data;
                                                                        
                        } catch (err) {
                            this.novedades= "ATENCION NO SE PUDIERON OBTENER LAS NOVEDADES"
                        }
                  },
    },
    mounted(){
       this.getNovedades().then(() => {
                this.loading = false;
       }); 
    }
}


</script>

