<script>
import { Line } from "vue-chartjs";
import moment from "moment";
export default {
  extends: Line,
  props: {
    estadisticas: {
      type: Array,
      default: null,
    },
  },
  data() {
    return {
      opciones: {},
      data: {},
    };
  },
  methods: {
    armarData() {
      this.data = {
        id: 4,
        data: {
          labels: this.armarLabels(),
          datasets: [
            {
              label: "",
              data: this.armarDataLabel(),
              borderColor: "#3182ce",
              backgroundColor: "rgba(72, 187, 120, 0.2)",
              fill: true,
            },
          ],
        },
      };
    },
    armarLabels() {
      var fechas = [];
      this.estadisticas.map(function (obj) {
        fechas.push(moment.utc(String(obj.fecha)).format("DD-MM-YYYY"));
      });
      return fechas;
    },
    armarDataLabel() {
      var montoCobradores = [];  
      this.estadisticas.map(function (obj) {
        montoCobradores.push(obj.Monto);
      });
      return montoCobradores;
    },

    setOpciones() {
      this.opciones = {
        options: {
          responsive: true,
          maintainAspectRatio: false,
          legend: {
            display: false,
          },
          scales: {
            xAxes: [
              {
                gridLines: {
                  display: false,
                  color: "rgba(0, 0, 0, 0)",
                  drawOnChartArea: false,
                  stacked: true,
                },
              },
            ],
            yAxes: [
              {
                display: false,
                gridLines: {
                  display: false,
                  color: "rgba(0, 0, 0, 0)",
                  drawOnChartArea: false,
                  stacked: true,
                },
              },
            ],
          },
        },
      };
    },
  },
  mounted() {
    this.armarData();
    this.setOpciones();
    this.renderChart(this.data.data, this.opciones.options);
  },
};
</script>
