  <b-container class="realizarEntrega">
    <br />
    <div v-if="loading" class="py-5 text-center box-shadow">
      <span class="text-danger">
        <b>Cargando</b>
      </span>
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
    </div>
    <div v-else>
      <b-row class="pb-3">
        <b-col lg="2" sm="4" class="text-center"></b-col>
        <b-col md="8" sm="6" class="text-left pt-3">
          <div>
            <p class="h4 text-center">Nueva Entrega</p>
          </div>
        </b-col>
      </b-row>
      <ValidationObserver ref="nuevaEntrega">
        <b-container class="pb-3">
          <b-row>
            <b-col lg="2" md="2">
              <b-form-group
                id="nroComprobante-label"
                label="Nro Comprobante:"
                label-for="nroComprobante"
              >
                <ValidationProvider
                  name="nroComprobante "
                  :rules="'required'"
                  v-slot="{ errors, valid }"
                >
                  <b-form-input
                    v-model="entrega.nroComprobante"
                    :state="errors[0] ? false : valid ? true : null"
                    size="sm"
                  ></b-form-input>
                  <b-form-invalid-feedback
                    v-for="error in errors"
                    :key="error.key"
                    >{{ error }}</b-form-invalid-feedback
                  >
                </ValidationProvider>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col lg="5" md="6">
              <b-form-group id="fecha-label" label="Fecha " label-for="fecha">
                <ValidationProvider
                  name="fecha "
                  :rules="'required'"
                  v-slot="{ errors, valid }"
                >
                  <b-form-input
                    v-model="entrega.fecha"
                    :state="errors[0] ? false : valid ? true : null"
                    size="sm"
                    type="date"
                  ></b-form-input>
                  <b-form-invalid-feedback
                    v-for="error in errors"
                    :key="error.key"
                    >{{ error }}</b-form-invalid-feedback
                  >
                </ValidationProvider>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col lg="2" md="2">
              <b-form-group
                id="Rendiciones-label"
                label="Rendiciones:"
                label-for="Rendiciones"
              >
                <ValidationProvider
                  :name="'Rendiciones '"
                  :rules="'required'"
                  v-slot="{ errors, valid }"
                >
                  <b-form-input
                    size="sm"
                    :state="errors[0] ? false : valid ? true : null"
                    v-model="entrega.cantrendiciones"
                    disabled
                  ></b-form-input>
                  <b-form-invalid-feedback
                    v-for="error in errors"
                    :key="error.key"
                    >{{ error }}</b-form-invalid-feedback
                  >
                </ValidationProvider>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col lg="2" md="2">
              <b-form-group
                id="cobrado-label"
                label="Cobrado:"
                label-for="cobrado"
              >
                <ValidationProvider
                  :name="'cobrado '"
                  v-slot="{ errors, valid }"
                >
                  <vue-numeric
                    style="width: 100px; text-align: right"
                    disabled
                    separator="."
                    :min="0"
                    :precision="2"
                    :max="999999"
                    :state="errors[0] ? false : valid ? true : null"
                    v-model="entrega.saldoTotalRendiciones"
                  ></vue-numeric>
                  <b-form-invalid-feedback
                    v-for="error in errors"
                    :key="error.key"
                    >{{ error }}</b-form-invalid-feedback
                  >
                </ValidationProvider>
              </b-form-group>
            </b-col>
            <b-col lg="2" md="2">
              <b-form-group
                id="Comision-label"
                label="Comision %:"
                label-for="Comision"
              >
                <ValidationProvider
                  :name="'Comision '"
                  v-slot="{ errors, valid }"
                >
                  <b-form-input
                    disabled
                    v-model="entrega.porcentajeComision"
                    :state="errors[0] ? false : valid ? true : null"
                    @change="calcularSaldo(entrega.porcentajeComision)"
                  ></b-form-input>
                  <b-form-invalid-feedback
                    v-for="error in errors"
                    :key="error.key"
                    >{{ error }}</b-form-invalid-feedback
                  >
                </ValidationProvider>
              </b-form-group>
            </b-col>
            <b-col lg="2" md="2">
              <b-form-group
                id="Entregar-label"
                label="A entregar:"
                label-for="Entregar"
              >
                <ValidationProvider
                  :name="'Entregar '"
                  v-slot="{ errors, valid }"
                >
                  <vue-numeric
                    style="width: 100px; text-align: right"
                    separator="."
                    :precision="2"
                    :state="errors[0] ? false : valid ? true : null"
                    v-model="entrega.saldo"
                    disabled
                  ></vue-numeric>
                  <b-form-invalid-feedback
                    v-for="error in errors"
                    :key="error.key"
                    >{{ error }}</b-form-invalid-feedback
                  >
                </ValidationProvider>
              </b-form-group>
            </b-col>
            <b-col lg="2" md="2">
              <b-form-group
                id="Entrega-label"
                label="Entrega:"
                label-for="Entrega"
              >
                <ValidationProvider
                  :name="'Entrega '"
                  :rules="'required'"
                  v-slot="{ errors }"
                >
                  <vue-numeric
                    style="width: 100px; text-align: right"
                    separator="."
                    :precision="2"
                    v-model="entrega.recibio"
                  ></vue-numeric>
                  <b-form-invalid-feedback
                    v-for="error in errors"
                    :key="error.key"
                    >{{ error }}</b-form-invalid-feedback
                  >
                </ValidationProvider>
              </b-form-group>
            </b-col>
          </b-row>
          <!--    <b-col lg="2" md="2">
              <b-form-group id="saldo-label" label="saldo:" label-for="saldo">
                <ValidationProvider :name="'saldo '" :rules="'required'" v-slot="{ errors,valid  }">
                  <b-form-input
                    size="sm"
                    :state="errors[0] ? false : (valid ? true : null)"
                    v-model="entrega.saldo"
                    disabled
                  ></b-form-input>
                  <b-form-invalid-feedback v-for="error in errors " :key="error.key">{{ error }}</b-form-invalid-feedback>
                </ValidationProvider>
              </b-form-group>
            </b-col> -->

          <b-row>
            <b-col cols="12">
              <b-form-group
                class="mb-0"
                id="observaciones-label"
                label="Observaciones"
                label-for="observaciones"
              >
                <b-form-textarea
                  id="observaciones"
                  size="sm"
                  rows="1"
                  v-model="entrega.observaciones"
                  placeholder="Observaciones"
                ></b-form-textarea>
              </b-form-group>
            </b-col>
          </b-row>
        </b-container>
      </ValidationObserver>
      <b-row>
        <b-col class="text-right">
          <div v-if="!creando">
            <b-button
              type="button"
              v-focus
              @keyup.enter="confirmarEntrega()"
              @click="confirmarEntrega()"
              variant="primary"
              >Generar</b-button
            >
          </div>
          <div v-else>
            <b-spinner variant="primary" label="Text Centered"></b-spinner>
          </div>
        </b-col>
      </b-row>
    </div>
  </b-container>
</template>


<script>
import CobradorService from "@/services/CobradorService";

export default {
  name: "nuevaFactura",
  components: {},
  props: {
    rendiciones: {
      type: Array,
    },
    entrega: {
      type: Object,
    },
  },
  data() {
    return {
      totalRendiciones: 0,
      loading: true,
      creando: false,
    };
  },

  directives: {
    focus: {
      inserted: function (el) {
        el.focus();
      },
    },
  },
  mounted() {
    this.calcularComision().then(() => {
      this.loading = false;
    });
  },
  created() {},
  methods: {
    async calcularComision() {
      try {
        const response = await CobradorService.porcentajeComision({
          cobrador: this.rendiciones[0].cobrador.id,
        });
        this.entrega.porcentajeComision = response.data.data;
        this.entrega.importeComision = response.data.data;
        if (this.entrega.porcentajeComision > 0) {
          this.entrega.importeComision =
            (this.entrega.porcentajeComision *
              this.entrega.saldoTotalRendiciones) /
            100;
          this.entrega.saldo =
            Number(this.entrega.saldoTotalRendiciones) -
            Number(this.entrega.importeComision);
          this.entrega.recibio = this.entrega.saldo;
        }
      } catch (err) {
        this.$bvToast.toast(`No se pudo obtener el valor de la comision`, {
          title: err.response.data,
          toaster: "b-toaster-top-center",
          solid: true,
          variant: "danger",
        });
      }
    },
    calcularSaldo(comision) {
      if (comision > 0) {
        this.entrega.saldo =
          Number(this.entrega.saldoTotalRendiciones) -
          Number((comision * this.entrega.saldoTotalRendiciones) / 100);
      }
    },
    async confirmarEntrega() {
      const valid = await this.$refs.nuevaEntrega.validate();
      if (valid) {
        this.$emit("confirmarEntrega");
      }
    },
  },
};
</script>

