<template>
  <b-container class="enviarMail">
    <br />
    <div v-if="loading">
      <span class="text-danger">
        <b>Cargando</b>
      </span>
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
    </div>
    <div v-else>
      <ValidationObserver ref="enviarMail">
        <b-row>
          <b-col lg="5" md="6">
            <b-form-group id="nombre-label" label="Nombre:" label-for="nombre">
              <ValidationProvider :name="'nombre '" :rules="'required'" v-slot="{ errors,valid  }">
                <b-form-input
                  size="sm"
                  v-model="persona.nombre"
                  disabled
                  :state="errors[0] ? false : (valid ? true : null)"
                ></b-form-input>
                <b-form-invalid-feedback v-for="error in errors " :key="error.key">{{ error }}</b-form-invalid-feedback>
              </ValidationProvider>
            </b-form-group>
          </b-col>
        </b-row>
        <b-row>
          <b-col lg="8" md="8">
            <b-form-group id="email-label" label="e-mail:" label-for="email">
              <ValidationProvider :name="'email '" :rules="'required'" v-slot="{ errors,valid  }">
                <b-form-input
                  @change="modificarMail()"
                  type="email"
                  v-model="persona.email"
                  v-focus
                  name="email"
                  :state="errors[0] ? false : (valid ? true : null)"
                ></b-form-input>
                <b-form-invalid-feedback v-for="error in errors " :key="error.key">{{ error }}</b-form-invalid-feedback>
              </ValidationProvider>
            </b-form-group>
          </b-col>
        </b-row>
      </ValidationObserver>
      <b-row>
        <b-col class="text-right">
          <div v-if="!creando">
            <b-button
                type="button"
                @click="descargarReciboFormatPDF()"
                @keyup.enter="descargarReciboFormatPDF()"
                variant="primary"
              >
            <b-icon icon="download" font-scale="1"></b-icon>
            </b-button>
            <b-button
             style="margin: 11px;"
              type="button"
              @click="setMail()"
              v-hotkey="{ enter: setMail}"
              variant="primary"
            >Enviar</b-button>
          </div>
          <div v-else>
            <b-spinner variant="primary" label="Text Centered"></b-spinner>
          </div>
        </b-col>
      </b-row>
    </div>
  </b-container>
</template>

<script>
import jsPDF from "jspdf";
import moment from 'moment'
import MailService from "@/services/MailService";
export default {
  name: "enviarMail",
  components: {},
  props: {
    persona: {
      type: Object
    },
     recibo: {
        type: Object
    },
    totalAPagar:{
      type:Number
    }
  },
  data() {
    return {
      creando: false,
      loading: true
    };
  },
  directives: {
    focus: {
      inserted: function(el) {
        el.focus();
      }
    }
  },
  methods: {
    modificarMail() {
      this.persona.modificoMail = true;
    },
    async setMail() {
      const valid = await this.$refs.enviarMail.validate();
      if (valid) {
        this.creando = true;
        try {
          const response = await MailService.sendEmail({
            email: this.persona.email,
            modificoMail: this.persona.modificoMail,
            dni: this.persona.dni,
            recibo: this.recibo.id
          });
          if (response.data.error) {
            this.$bvToast.toast(`Atencion! `, {
              title: response.data.data,
              toaster: "b-toaster-top-center",
              solid: true,
              variant: "danger"
            });
          } else {
            this.$emit("realizarEnvioMail");
          }
        } catch (error) {
          this.$bvToast.toast(`No se pudo enviar el mail`, {
            title: error.response.data.data,
            toaster: "b-toaster-top-center",
            solid: true,
            variant: "danger"
          });
        }
        this.creando = false;
      }
    },
    async getMailPersona() {
      try {
        const response = await MailService.getMailPersona({
          dni: this.persona.dni
        });
        this.persona.email = response.data.data;
      } catch (err) {
        alert(err.response.data);
      }
    },
    descargarReciboFormatPDF() { 
      var fechaHoy = new Date();
      var fechaLetras = { year: "numeric", month: "long", day: "numeric" };
      var fechaDMY= moment.utc(String( this.recibo.fecha)).format('DD/MM/YYYY')
      const doc = new jsPDF('p', 'mm',  [190,700]);
      doc.setTextColor(100);
      doc.setFontSize(35);
      doc.text(20, 10, "Comprobante de pago");     	
      doc.setTextColor(0);     
      doc.setFontSize(22);
      doc.setFont('Calibri');
      doc.text(60,20,
        "Dolores, " + fechaHoy.toLocaleDateString("es-ES", fechaLetras)
      );
      doc.setFontSize(28);
      doc.text(5,40,
        "Recibimos")
      doc.setFontSize(24);
      doc.text (5,60,"De: "+this.persona.nombre);
      doc.setFontSize(28);
      doc.text(5,75,
         "Monto: " + this.getImporteDecimal(this.recibo.importe));  
      if (this.totalAPagar == this.recibo.importe) {
        doc.text(10, 90,
          'Pago total de los conceptos adeudados a');
        doc.text(10,105,'"Club Atletico Ever Ready" el '+fechaDMY)
        doc.text(20,120,
                "TOTAL: " + this.getImporteDecimal(this.recibo.importe));
      } else {
        //let saldo =Number(this.totalAPagar) - Number(this.recibo.importe);
        doc.text(10,90,'Pago parcial de los conceptos adeudados a')
        doc.text (10,105,'"Club Atletico Ever Ready" el '+fechaDMY) 
        doc.text(20,120 ,
          "TOTAL: " + this.getImporteDecimal(this.totalAPagar)
        );
        doc.text(20,135 ,
          "ABONADO: " + this.getImporteDecimal(this.recibo.importe)
        );
        //doc.text(20, 150, "SALDO: " + this.getImporteDecimal(saldo));
      } 
      doc.addImage('data:image/png;base64,', "JPEG", 115, 125, 70,70);
      doc.autoPrint();
      doc.output('dataurlnewwindow', {filename: 'Comprobante-pago.pdf'});     
    },
     getImporteDecimal(importe) {
      const options2 = { style: "currency", currency: "USD" };
      const numberFormat2 = new Intl.NumberFormat("en-US", options2);
      return numberFormat2.format(importe);
    },
  },
  mounted() {
    this.getMailPersona().then(() => {
      this.loading = false;
    });
  }
};
</script>

