<template>
  <b-container class="nuevaRendicion">
    <br />
    <div v-if="loading" class="py-5 text-center box-shadow">
      <span class="text-danger">
        <b>Cargando</b>
      </span>
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
    </div>
    <div v-else class="box-shadow p-2 mx-1">
      <b-row class="pb-3">
        <b-col lg="2" sm="4" class="text-center"></b-col>
        <b-col md="8" sm="6" class="text-left pt-3">
          <div>
            <p class="h4 text-center">Nueva Rendicion</p>
          </div>
        </b-col>
      </b-row>
      <ValidationObserver ref="nuevaRendicion">
        <b-container class="pb-3">
          <b-card class="mb-3" header="Rendicion">
            <b-row>
              <b-col lg="5" md="6">
                <b-form-group id="fecha-label" label="Fecha " label-for="fecha">
                  <ValidationProvider
                    name="fecha "
                    :rules="'required'"
                    v-slot="{ errors, valid }"
                  >
                    <b-form-input
                      v-model="rendicion.fecha"
                      :state="errors[0] ? false : valid ? true : null"
                      size="sm"
                      type="date"
                      @change="calcularImporteDesdeFecha(rendicion.empresa)"
                    ></b-form-input>
                    <b-form-invalid-feedback
                      v-for="error in errors"
                      :key="error.key"
                      >{{ error }}</b-form-invalid-feedback
                    >
                  </ValidationProvider>
                </b-form-group>
              </b-col>

              <b-col lg="5" md="6">
                <b-form-group
                  id="empresa-label"
                  label="Empresa:"
                  label-for="empresa"
                >
                  <ValidationProvider
                    :name="'empresa '"
                    :rules="'required'"
                    v-slot="{ errors, valid }"
                  >
                    <b-form-select
                      size="sm"
                      :state="errors[0] ? false : valid ? true : null"
                      v-model="rendicion.empresa"
                      :options="fEmpresasSelect"
                      @change="calcularImporte()"
                    ></b-form-select>
                    <b-form-invalid-feedback
                      v-for="error in errors"
                      :key="error.key"
                      >{{ error }}</b-form-invalid-feedback
                    >
                  </ValidationProvider>
                </b-form-group>
              </b-col>

              <b-col lg="2" md="2">
                <b-form-group
                  id="importe-label"
                  label="Importe:"
                  label-for="importe"
                >
                  <ValidationProvider
                    :name="'importe '"
                    :rules="'required'"
                    v-slot="{ errors, valid }"
                  >
                    <vue-numeric
                      v-model="importeRecibo"
                      disabled
                      style="text-align: right"
                      size="10px"
                      separator="."
                      :min="0"
                      :precision="2"
                      :max="999999"
                      :state="errors[0] ? false : valid ? true : null"
                    ></vue-numeric>

                    <b-form-invalid-feedback
                      v-for="error in errors"
                      :key="error.key"
                      >{{ error }}</b-form-invalid-feedback
                    >
                  </ValidationProvider>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <b-col cols="12">
                <b-form-group
                  class="mb-0"
                  id="observaciones-label"
                  label="Observaciones"
                  label-for="observaciones"
                >
                  <b-form-textarea
                    id="observaciones"
                    size="sm"
                    rows="1"
                    v-model="rendicion.observaciones"
                    placeholder="Observaciones"
                  ></b-form-textarea>
                </b-form-group>
              </b-col>
            </b-row>
          </b-card>
        </b-container>
      </ValidationObserver>
      <b-row>
        <b-col class="text-left">
          <b-button @click="volver()" variant="outline-secondary">
            <b-icon icon="arrow-left-short"></b-icon>Volver
          </b-button>
        </b-col>
        <b-col class="text-right">
          <b-button
            type="button"
            @click="verRecibos()"          
            variant="secondary"
              :disabled="importeRecibo == 0"
          >
            <b-icon icon="eye"></b-icon>Recibos
          </b-button>
          <b-button
            style="margin: 11px"
            v-if="!creando"
            type="button"
            @click="setRendicion()"
            :disabled="importeRecibo == 0"
            variant="primary"
            >Generar</b-button
          >
        </b-col>
      </b-row>
      <div id="ModalRecibos">
        <b-modal size="lg" ref="modalRecibos" ok-only>
          <ListadoRecibos :items="recibos"></ListadoRecibos>
        </b-modal>
      </div>
    </div>
  </b-container>
</template>


<script>
import EmpresaService from "@/services/EmpresaService";
import RendicionService from "@/services/RendicionService";
import ReciboService from "@/services/ReciboService";
import ListadoRecibos from "@/components/recibo/ListadoRecibos.vue";
import { mapGetters } from "vuex";
export default {
  name: "nuevaFactura",
  components: {
    ListadoRecibos,
  },
  data() {
    return {
      rendicion: {
        empresa: null,
        fecha: new Date().toISOString().slice(0, 10),
        observaciones: null,
      },
      loading: true,
      creando: false,
      empresas: [],
      recibos: [],
      importeRecibo: 0,
    };
  },
  created() {},
  computed: {
    ...mapGetters("storeUser", ["getUserId", "getGrupos"]),
    fEmpresasSelect() {
      let mc = this.empresas.map((e) => ({ value: e.id, text: e.nombre }));
      mc.push({
        value: null,
        text: "-- Seleccione una empresa --",
        disabled: true,
      });
      return mc;
    },
  },
  mounted() {
    this.listadoEmpresas().then(() => {
      this.loading = false;
    });
  },
  methods: {
    volver() {
      this.$router.push({ name: "ListadoRendiciones" });
    },
    calcularImporteDesdeFecha(empresa) {
      if (empresa != null) {
        this.calcularImporte();
      }
    },
    async calcularImporte() {
      const response = await ReciboService.getRecibosEnFechaDeEmpresaDeCobrador(
        {
          fecha: this.rendicion.fecha,
          cobrador: this.getUserId,
          empresa: this.rendicion.empresa,
        }
      );
      this.recibos = response.data.data;
      this.importeRecibo = 0;
      this.recibos.forEach(
        (recibo) =>
          (this.importeRecibo =
            Number(this.importeRecibo) + Number(recibo.importe))
      );
    },

    verRecibos() {
      this.$refs["modalRecibos"].show();
    },

    async setRendicion() {
      const valid = await this.validate();
      if (valid) {
        this.creando = true;
        try {
          const response = await RendicionService.addRendicion({
            empresa: this.rendicion.empresa,
            importe: this.importeRecibo,
            fecha: this.rendicion.fecha,
            observaciones: this.rendicion.observaciones,
            cobrador: this.getUserId,
            recibos: this.recibos,
          });
          this.$root.$bvToast.toast(`Rendicion generada `, {
            title: response.data.data,
            toaster: "b-toaster-top-center succes",
            solid: true,
            variant: "success",
          });
          this.creando = response.data.error;
          this.$router.push({ name: "ListadoRendiciones" });
        } catch (error) {
          error.response.data.data.forEach((data) => {
            this.$bvToast.toast(`No se pudo generar La rendicion`, {
              title: data,
              toaster: "b-toaster-top-center",
              solid: true,
              variant: "danger",
            });
          });
        }
      }
    },
    async validate() {
      if (this.loading) {
        return false;
      } else {
        let result = await this.$refs.nuevaRendicion.validate();
        return result;
      }
    },

    async listadoEmpresas() {
      try {
        const response = await EmpresaService.getEmpresas();
        this.empresas = response.data.data;
      } catch (err) {
        alert(err.response.data);
      }
    },
  },
};
</script>

