import Api from '@/services/Api'
const API_USER = "";
const API_URL = "api/";

export default {
    getEstadisticasCobradores(credentials) {    
        return Api().get(API_URL + API_USER + "get_estadisticas_cobradores/"+ credentials.empresa)
    },

    getEstadisticasSinRendicion(credentials) {    
        return Api().get(API_URL + API_USER + "get_estadisticas_sinRendicion/"+ credentials.empresa)
    },    
    getEstadisticasFacturas(credentials) {    
        return Api().get(API_URL + API_USER + "get_estadisticas_facturas/"+ credentials.empresa)
    },
    getEstadisticasCobradoPorFecha(credentials) {    
        return Api().get(API_URL + API_USER + "get_estadisticas_cobrado_fecha/"+ credentials.empresa)
    },
    getEstadisticasPorcentajeCobrado(credentials) {    
        return Api().get(API_URL + API_USER + "get_porcentaje_cobrado/"+ credentials.empresa)
    }
}