import Vue from 'vue'
import Router from 'vue-router'
import store from './store.js'


/* INICIO */
import Login from '@/components/Inicio/Login.vue'
import Registrarse from '@/components/Inicio/Registrar.vue'
/* Home */
import Novedades from '@/components/home/Novedades.vue'
/* Menu */
import Container from '@/components/menu/Container.vue'

/* Factura */
import NuevaFactura from '@/components/facturas/NuevaFactura.vue'

import ListadoFacturas from '@/components/facturas/ListadoFacturas.vue'
/* Recibo */
import NuevoRecibo from '@/components/recibo/NuevoRecibo.vue'
import HistorialRecibosPagados from '@/components/recibo/HistorialRecibosPagados.vue'
/* Cobro */
import NuevoCobro from '@/components/cobro/NuevoCobro.vue'
import ListadoCobros from '@/components/cobro/ListadoCobros.vue'

/* Rendicion */
import NuevaRendicion from '@/components/rendicion/NuevaRendicion.vue'
import ListadoRendiciones from '@/components/rendicion/ListadoRendiciones.vue'
import NuevaEntrega from '@/components/rendicion/NuevaEntrega.vue'
/* Estadisticas */
import Estadisticas from '@/components/estadisticas/Estadisticas.vue'
Vue.use(Router)
const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [{
    path: '/',
    component: Container ,
    meta: { 
      requireAuth: true
    },
    children: [{ 
        path: "/",
        component: Novedades ,
          name:'Novedades',
          },     
      { 
          path: "/nuevafactura",
          component: NuevaFactura ,
          name:'nuevaFactura',
        },
       
         { 
          path: "/nuevorecibo",
          component: NuevoRecibo ,
          name:'NuevoRecibo',
        },
        { 
          path: "/HistorialRecibosPagados",
          component: HistorialRecibosPagados ,
          name:'HistorialRecibosPagados',
        },
        { 
          path: "/nuevocobro",
          component: NuevoCobro ,
          name:'NuevoCobro',
        },
        { 
          path: "/listadocobros",
          component: ListadoCobros ,
          name:'ListadoCobros',
        },
        { 
          path: "/nuevarendicion",
          component: NuevaRendicion ,
          name:'NuevaRendicion',
        },
        { 
          path: "/listadorendiciones",
          component: ListadoRendiciones ,
          name:'ListadoRendiciones',
        },
      
        { 
          path: "/ListadoFacturas",
          component: ListadoFacturas ,
          name:'ListadoFacturas',
        },
        { 
          path: "/nuevaentrega",
          component: NuevaEntrega ,
          name:'NuevaEntrega',
        },
        { 
          path: "/estadisticas",
          component: Estadisticas ,
          name:'Estadisticas',
        },
      ]
    },
    {
      path: '/login',    
      component: () => import('@/components/Inicio/BaseLogin.vue'), 
      meta: {
        requireAuth: false
      },
      children: [{
          path: '/login',
          name: 'Login',
          component: Login,
        },
        {
          path: '/registrar',
          name: 'Registrarse',
          component: Registrarse
        },     
      ]
    },
    {
      path: '/.well-known/acme-challenge/:file'
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})


router.beforeEach((to, from, next) => {
  const requireAuth = to.matched.some(record => record.meta.requireAuth)

  if (requireAuth && !store.getters['storeUser/isAuthenticated']) {
    next('/login')
  } else if (store.getters['storeUser/isAuthenticated'] && to.name == 'Login') {   
    next('/')
  } else {
    if (!to.matched.length) {     
      next('/');
    } else {     
      next();
    }
   
  }
})


export default router

