import Api from '@/services/Api'
const API_USER = "";
const API_URL = "api/";

export default {

    addRendicion(credentials) {
    
        return Api().post(API_URL + API_USER + "nueva_rendicion", credentials)
    },
    getRendicionesDe(credentials){
        return Api().post(API_URL + API_USER + "rendicion_de_entidad", credentials)
    },
    getEstadosRendiciones(credentials){
        return Api().get(API_URL + API_USER + "get_estados_rendiciones", credentials)
       
    },
    rendir_rendicion(credentials){
        return Api().post(API_URL + API_USER + "rendir_rendicion", credentials)
       
    },

   
  
}