<template>
  <b-container class="nuevoRecibo">
    <br />

    <div v-if="loading" class="py-5 text-center box-shadow">
      <span class="text-danger">
        <b>Cargando</b>
      </span>
    </div>
    <div v-else class="box-shadow p-2 mx-1">
      <b-row class="pb-3">
        <b-col lg="2" sm="4" class="text-center"></b-col>
        <b-col md="8" sm="6" class="text-left pt-3">
          <div>
            <p class="h4 text-center">Nuevo Recibo</p>
          </div>
        </b-col>
      </b-row>
      <ValidationObserver ref="nuevoRecibo">
        <b-container class="pb-3">
          <b-form-group>
            <b-alert
              v-for="alert in alerts"
              dismissible
              v-bind:key="alert.key"
              show
              :variant="alert.variant"
              >{{ alert.message }}</b-alert
            >
          </b-form-group>
          <b-card class="mb-3" header="Usuario">
            <b-row>
              <b-col lg="5" md="6">
                <b-form-group id="DNI-label" label="DNI:" label-for="DNI">
                  <ValidationProvider
                    :name="'DNI '"
                    :rules="'required'"
                    v-slot="{ errors, valid }"
                  >
                    <b-form-input
                      size="sm"
                      v-model="persona.dni"
                      disabled
                      :state="errors[0] ? false : valid ? true : null"
                    ></b-form-input>
                    <b-form-invalid-feedback
                      v-for="error in errors"
                      :key="error.key"
                      >{{ error }}</b-form-invalid-feedback
                    >
                  </ValidationProvider>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <b-col lg="5" md="6">
                <b-form-group
                  id="nombre-label"
                  label="Nombre:"
                  label-for="nombre"
                >
                  <ValidationProvider
                    :name="'nombre '"
                    :rules="'required'"
                    v-slot="{ errors, valid }"
                  >
                    <b-form-input
                      size="sm"
                      v-model="persona.nombre"
                      disabled
                      :state="errors[0] ? false : valid ? true : null"
                    ></b-form-input>
                    <b-form-invalid-feedback
                      v-for="error in errors"
                      :key="error.key"
                      >{{ error }}</b-form-invalid-feedback
                    >
                  </ValidationProvider>
                </b-form-group>
              </b-col>
            </b-row>
          </b-card>
          <b-card class="mb-3" header="Recibo">
            <b-row>
              <b-col lg="5" md="6">
                <b-form-group
                  id="numero-label"
                  label="Numero Recibo:"
                  label-for="numero"
                >
                  <ValidationProvider
                    :name="'numero '"
                    :rules="'required'"
                    v-slot="{ errors, valid }"
                  >
                    <b-form-input
                      size="sm"
                      disabled
                      :state="errors[0] ? false : valid ? true : null"
                      v-model="recibo.numero"
                      type="number"
                    ></b-form-input>
                    <b-form-invalid-feedback
                      v-for="error in errors"
                      :key="error.key"
                      >{{ error }}</b-form-invalid-feedback
                    >
                  </ValidationProvider>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <b-col lg="5" md="6">
                <b-form-group id="fecha-label" label="Fecha " label-for="fecha">
                  <ValidationProvider
                    name="fecha "
                    :rules="'required'"
                    v-slot="{ errors, valid }"
                  >
                    <b-form-input
                      disabled
                      v-model="recibo.fecha"
                      :state="errors[0] ? false : valid ? true : null"
                      size="sm"
                      type="date"
                    ></b-form-input>
                    <b-form-invalid-feedback
                      v-for="error in errors"
                      :key="error.key"
                      >{{ error }}</b-form-invalid-feedback
                    >
                  </ValidationProvider>
                </b-form-group>
              </b-col>
              <b-col lg="5" md="6">
                <b-form-group
                  id="importe-label"
                  label="Importe:"
                  label-for="importe"
                >
                  <ValidationProvider
                    :name="'importe '"
                    :rules="'required'"
                    v-slot="{ errors, valid }"
                  >
                    <vue-numeric
                      style="text-align: right"
                      v-model="recibo.importe"
                      disabled
                      separator="."
                      :min="0"
                      :precision="2"
                      :max="999999"
                      :state="errors[0] ? false : valid ? true : null"
                    ></vue-numeric>
                    <b-form-invalid-feedback
                      v-for="error in errors"
                      :key="error.key"
                      >{{ error }}</b-form-invalid-feedback
                    >
                  </ValidationProvider>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row>
              <b-col cols="12">
                <b-form-group
                  class="mb-0"
                  id="observaciones-label"
                  label="Observaciones"
                  label-for="observaciones"
                >
                  <b-form-textarea
                    v-focus
                    id="observaciones"
                    size="sm"
                    rows="1"
                    v-model="recibo.observaciones"
                    placeholder="Observaciones"
                  ></b-form-textarea>
                </b-form-group>
              </b-col>
            </b-row>
          </b-card>
        </b-container>
        <b-row>
          <b-col class="text-right">
            <div v-if="!creando">
              <b-button
                type="button"
                style="margin: 11px"
                @click="setRecibo()"
                @keyup.enter="setRecibo()"
                :disabled="creoRecibo"
                variant="primary"
                >Generar</b-button
              >
            </div>
            <div v-else>
              <b-spinner variant="primary" label="Text Centered"></b-spinner>
            </div>
          </b-col>
        </b-row>
      </ValidationObserver>
    </div>

    <div id="ModalEnvioMailImprimirRecibo">
      <b-modal
        ref="ModalEnvioMailImprimirRecibo"
        title="Enviar mail e imprimir recibo"
        @hidden="realizarReciboSinEnvioMail"
        hide-footer
      >
        <EnviarMail
          @realizarEnvioMail="realizarEnvioMail"
          :persona="this.persona"
          :totalAPagar="this.$route.params.totalPagar"
          :recibo="this.recibo"
        ></EnviarMail>
      </b-modal>
    </div>
  </b-container>
</template>

<script>
import { mapGetters } from "vuex";
import { mapActions } from "vuex";
import ReciboService from "@/services/ReciboService";
import EnviarMail from "@/components/mail/EnviarMail.vue";

export default {
  name: "nuevaFactura",
  components: {
    EnviarMail,
  },
  data() {
    return {
      recibo: {
        id: null,
        codigo: null,
        numero: null,
        importe: null,
        fecha: null,
        observaciones: null,
      },
      alerts: [],
      loading: false,
      creando: false,
      creoRecibo: false,
      persona: {
        dni: "",
        nombre: "",
        modificoMail: "",
        email: "",
      },
      rendicion: {
        nombre: "",
      },
    };
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus();
      },
    },
  },
  created() {
    this.generarNumeroRecibo();
    this.completarRecibo(
      this.$route.params.facturas,
      this.$route.params.totalPagar,
      this.$route.params.entrega
    );
  },
  computed: {
    ...mapGetters("storeUser", ["getUserId", "getGrupos"]),
  },

  methods: {
    ...mapActions(["showToast"]),
    completarRecibo(facturas, totalPagar, entrega) {
      this.persona.nombre = facturas[0].nombre;
      this.persona.dni = facturas[0].nroDoc;
      this.recibo.importe = entrega;
      this.recibo.fecha = new Date().toISOString().slice(0, 10);
    },
    setRecibo() {
      this.generarRecibo();
    },
    async generarRecibo() {
      const valid = await this.$refs.nuevoRecibo.validate();
      if (valid) {
        this.creando = true;
        try {
          const response = await ReciboService.addRecibo({
            facturas: this.$route.params.facturas,
            codigoRecibo: this.recibo.codigo,
            numeroRecibo: this.recibo.numero,
            importeRecibo: this.recibo.importe,
            fechaRecibo: this.recibo.fecha,
            observacionesRecibo: this.recibo.observaciones,
            usuario: this.persona,
            idCobrador: this.getUserId,
          });
          this.creoRecibo = true;
          if (response.data.data != null) {
            this.recibo.id = response.data.data;
            this.$refs["ModalEnvioMailImprimirRecibo"].show();
          }
        } catch (error) {
          this.$root.$bvToast.toast(`No se pudo generar el recibo`, {
            title: error.response.data.data,
            toaster: "b-toaster-top-center",
            solid: true,
            variant: "danger",
          });
          this.$router.push({ name: "NuevoCobro" });
        }
        this.creando = false;
      }
    },
    realizarEnvioMail() {
      this.$refs["ModalEnvioMailImprimirRecibo"].hide();
      this.$root.$bvToast.toast(`Usted genero el recibo y envio el mail `, {
        title: "GENERO CON EXITO EL RECIBO",
        toaster: "b-toaster-top-center succes",
        solid: true,
        variant: "success",
      });
      this.$router.push({ name: "NuevoCobro" });
    },
    realizarReciboSinEnvioMail() {
      this.$refs["ModalEnvioMailImprimirRecibo"].hide();
      this.$root.$bvToast.toast(
        `Usted genero el recibo, pero no envio el mail`,
        {
          title: "GENERO CON EXITO EL RECIBO",
          toaster: "b-toaster-top-center succes",
          solid: true,
          variant: "success",
        }
      );
      this.$router.push({ name: "NuevoCobro" });
    },
    async validate() {
      if (this.loading) {
        return false;
      } else {
        let result = await this.$refs.nuevoRecibo.validate();
        return result;
      }
    },
    getImporteDecimal(importe) {
      const options2 = { style: "currency", currency: "USD" };
      const numberFormat2 = new Intl.NumberFormat("en-US", options2);
      return numberFormat2.format(importe);
    },
    async generarNumeroRecibo() {
      try {
        const response = await ReciboService.newNumeroRecibo();
        this.recibo.numero = response.data.data;
      } catch (err) {
        this.$root.$bvToast.toast(`No se pudo obtener el numero del recibo`, {
          title: err.response.data,
          toaster: "b-toaster-top-center",
          solid: true,
          variant: "danger",
        });
      }
    },
  },
};
</script>

