import Api from '@/services/Api'
const API_USER = "";
const API_URL = "api/";

export default {

    addRecibo(credentials) {
    
        return Api().post(API_URL + API_USER + "nuevo_recibo", credentials)
    },

    newNumeroRecibo(credentials) {    
        return Api().get(API_URL + API_USER + "get_new_numero_recibo", credentials)
    },
    getRecibosEnFechaDeEmpresaDeCobrador(credentials){
         return Api().post(API_URL + API_USER + "getrecibos_sinrendiciones_fecha_cobrador_empresa", credentials) 
    },
    getRecibosDeRendicion(credentials){
        return Api().post(API_URL + API_USER + "get_recibos_rendicion", credentials) 
   },
   getRecibosPagadosdeCliente(credentials){       
    return Api().get(API_URL + API_USER + "recibos_pagados_usuario/"+credentials.dniOsocio) 
},

  
}